import react from '@servicenow/ui-renderer-react';
import { Component, PropTypes, propsToAttributes } from '../../utils/seismic';
import { t } from 'sn-translate';
import _, { forEach, isArray } from 'lodash';

import * as DependencyManager from '../../../config/DependencyManager';
import { createResizableController } from '../../utils/resizableController';
import { ResizeBarElement , EmptyStateElement } from '@servicenow/now-record-common';
import {
	MIN_LEFT_COLUMN_WIDTH,
	MIN_RIGHT_COLUMN_WIDTH
} from './layoutConstants';
import { SELECTED_LAYOUT_WILL_CHANGE ,
	FORM_ACTIVITY_STREAM_LAYOUT_RESIZED,
	FORM_ACTIVITY_LAYOUT_TYPE_CHANGED,
	RESET_SELECTED_SECTION_INDEX
} from './columnLayoutActions';
import { default as Section } from '../section/Section';
import { default as ToolbarElement } from '../toolbar/toolbarElement';
import {
	getLayoutButtons,
	getLayoutType,
	LAYOUT_TYPE,
	getSelectedLayoutButtonIndex
} from './formLayoutHelper';
import classNames from 'classnames/dedupe';

import renderReadonlyNotification from '../../common/renderReadOnlyNotification';
import '@servicenow/now-dropdown';
import { GFORM_ACTIONS } from '@devsnc/sn-scripting-environment';

import { getProperty } from 'sn-uxpage-presource';

// React components
const useSeismic = false;
const IS_HIDDEN_CLASS_NAME = 'is-hidden';
const LAYOUT_COMPONENTS = {
	FORM: 'form',
	RESIZE_BAR: 'resizeBar',
	ACTIVITY: 'activity'
};
const SECTION_DROPDOWN_ARIA_LABEL = t('Jump to section navigation');

const ALLOW_KEY_ACCESS_RESIZE_BAR = false;
const ENABLE_FIRST_FOCUSABLE_ELEMENT = getProperty(
	'glide.ui.focus_first_element',
	false
);

class ColumnLayout extends Component {
	constructor(props) {
		super(props);
		const {
			config: { showActivityStream, isNewRecord },
			table,
			sysId,
			hasErrorMessage,
			formLayoutConfig = {},
			activityStream,
			fields,
			sections
		} = this.props.properties;
		let selectedLayoutType = showActivityStream
			? formLayoutConfig.formLayoutType
			: LAYOUT_TYPE.FORM;
		if (!activityStream || !activityStream.events)
			selectedLayoutType = LAYOUT_TYPE.FORM;
		else if (selectedLayoutType === LAYOUT_TYPE.ACTIVITY && hasErrorMessage)
			selectedLayoutType = LAYOUT_TYPE.HALF;

		if (isNewRecord && ENABLE_FIRST_FOCUSABLE_ELEMENT)
			this.focusFirstElement(sections, fields);

		this.state = {
			selectedLayoutType,
			activated: this.getActivateComponents(selectedLayoutType)
		};
	}

	getActivateComponents = layoutType => {
		switch (layoutType) {
			case LAYOUT_TYPE.FORM:
				return {
					[LAYOUT_COMPONENTS.FORM]: true,
					[LAYOUT_COMPONENTS.RESIZE_BAR]: false
				};
			case LAYOUT_TYPE.ACTIVITY:
				return {
					[LAYOUT_COMPONENTS.FORM]: false,
					[LAYOUT_COMPONENTS.RESIZE_BAR]: false,
					[LAYOUT_COMPONENTS.ACTIVITY]: true
				};
			case LAYOUT_TYPE.HALF:
				return {
					[LAYOUT_COMPONENTS.FORM]: true,
					[LAYOUT_COMPONENTS.RESIZE_BAR]: true,
					[LAYOUT_COMPONENTS.ACTIVITY]: true
				};
			default:
				return {};
		}
	};

	leftColumnRef = ref => {
		if (!ref) return;

		if (this.leftColumn !== ref) {
			this.leftColumn = ref;
			this.setupResizableWhenReady();
		}
	};

	containerRef = ref => {
		if (!ref) return;

		if (this.container !== ref) {
			this.container = ref;
			this.setupResizableWhenReady();
		}
	};

	resizeBarRef = ref => {
		if (!ref) return;

		if (this.resizeBar != ref) {
			this.resizeBar = ref;
			this.setupResizableWhenReady();
		}
	};

	setupResizableWhenReady = () => {
		if (!this.container || !this.leftColumn || !this.resizeBar) return;

		this.resizableController = createResizableController(
			this.container,
			this.leftColumn,
			this.resizeBar,
			this.resizeKey,
			{
				minLeftColumnWidth: MIN_LEFT_COLUMN_WIDTH,
				minRightColumnWidth: MIN_RIGHT_COLUMN_WIDTH,
				allowKeyAccess: ALLOW_KEY_ACCESS_RESIZE_BAR,
				beforeStartResizing: () => {
					this.setState({
						isResizing: true
					});
				},
				afterStopResizing: formSizeRatio => {
					this.props.dispatch(FORM_ACTIVITY_STREAM_LAYOUT_RESIZED, {
						table: this.props.properties.table,
						formSizeRatio: formSizeRatio
					});
					this.setState({
						isResizing: false
					});
				}
			}
		);
		this.resizableController.initialize();
		if (this.state.selectedLayoutType === LAYOUT_TYPE.HALF)
			this.resizableController.enableResizing();
	};

	componentDidUpdate(prevProps) {
		if (this.props.selectedSectionIndex !== prevProps.selectedSectionIndex) {
			this.scrollToSection(this.props.selectedSectionIndex);
		}
	}

	componentWillUnmount() {
		if (this.resizableController) this.resizableController.destroy();
	}

	componentWillReceiveProps(changedProps) {
		const { hasErrorMessage, formLayoutConfig, config: { showActivityStream } } = changedProps.properties;
		if (hasErrorMessage !== this.props.properties.hasErrorMessage) {
			if (
				this.selectedLayoutType === LAYOUT_TYPE.ACTIVITY &&
				hasErrorMessage
			) {
				this.selectedLayoutType = LAYOUT_TYPE.HALF;
			}
		}

		if (showActivityStream !== this.props.properties.config.showActivityStream) {
			this.selectedLayoutType = showActivityStream
				? formLayoutConfig.formLayoutType
				: LAYOUT_TYPE.FORM;
		}
	}

	/**
	 * Apply changes from activity stream to client side form model
	 * @param  {[{}]} changedFields Array of field objects
	 */
	onInputChange = changedFields => {
		if (!isArray(changedFields)) {
			return;
		}

		forEach(changedFields, fieldObject => {
			const { field, value, displayValue } = fieldObject;
			this.props.dispatch(GFORM_ACTIONS.GFORM_SET_VALUE, {
				fieldName: field,
				value,
				displayValue
			});
		});
	};

	toolbarButtonClick = button => {
		const selectedLayoutType = getLayoutType(button);
		this.selectedLayoutType = selectedLayoutType;
	};

	set selectedLayoutType(selectedLayoutType) {
		this.props.dispatch(FORM_ACTIVITY_LAYOUT_TYPE_CHANGED, {
			table: this.props.properties.table,
			selectedLayoutType: selectedLayoutType
		});

		let minWidth;
		switch (selectedLayoutType) {
			case LAYOUT_TYPE.FORM:
				minWidth = MIN_LEFT_COLUMN_WIDTH;
				if (this.resizableController)
					this.resizableController.disableResizing();
				break;
			case LAYOUT_TYPE.ACTIVITY:
				minWidth = MIN_RIGHT_COLUMN_WIDTH;
				if (this.resizableController)
					this.resizableController.disableResizing();
				break;
			case LAYOUT_TYPE.HALF:
				minWidth = MIN_LEFT_COLUMN_WIDTH + MIN_RIGHT_COLUMN_WIDTH;
				if (this.resizableController)
					this.resizableController.enableResizing();
				break;
		}

		this.props.dispatch(SELECTED_LAYOUT_WILL_CHANGE, {
			selectedLayoutType,
			minWidth
		});

		this.setState({
			selectedLayoutType,
			activated: {
				...this.state.activated,
				...this.getActivateComponents(selectedLayoutType)
			}
		});
	}

	get selectedLayoutType() {
		return this.state.selectedLayoutType;
	}

	shouldHideComponent = component => {
		switch (component) {
			case LAYOUT_COMPONENTS.FORM:
				return this.selectedLayoutType === LAYOUT_TYPE.ACTIVITY;
			case LAYOUT_COMPONENTS.ACTIVITY:
				return this.selectedLayoutType === LAYOUT_TYPE.FORM;
			case LAYOUT_COMPONENTS.RESIZE_BAR:
				return this.selectedLayoutType !== LAYOUT_TYPE.HALF;
		}
		return false;
	};

	shouldRenderComponent = component => {
		return (
			!this.shouldHideComponent(component) || this.isActivated(component)
		);
	};

	isActivated(component) {
		return this.state.activated[component] === true;
	}

	scrollToSection(index) {
		if (!this.leftColumn) return;
		const {
			properties: {
				inUiBuilder,
				scrollableHost
			},
			host
		} = this.props;

		if (index > -1) {
			const sectionList = this.leftColumn.getElementsByClassName(
				'sn-section'
			);
			/**
			 * @type {HTMLElement}
			 */
			const desiredSection = _.nth(sectionList, index);

			if (desiredSection) {
			/**
			 * @type {HTMLElement} 
			 */
			const scrollableSection = !inUiBuilder ? _.nth(
				this.leftColumn.getElementsByClassName(
					'sn-form-column-layout-sections'
				),
				0
			) : scrollableHost || host;

				const offsetTopFromScrollableHost = scrollableHost && host ?  host.getBoundingClientRect().top - scrollableHost.getBoundingClientRect().top + scrollableHost.scrollTop : 0;
				const scrollTop = offsetTopFromScrollableHost + desiredSection.offsetTop;

				if (scrollableSection.scrollTop !== scrollTop) {
					const propScrollDebouncer = 'scrollDebouncer';
					/**
					 * @type { (() => void) & _.Cancelable }
					 */
					const oldDebouncerFn =
						scrollableSection[propScrollDebouncer];
					const debounceFn = _.debounce(() => {
						scrollableSection[propScrollDebouncer] = null;
						scrollableSection.removeEventListener(
							'scroll',
							debounceFn
						);
						desiredSection.querySelector('header').focus();
						this.props.dispatch(RESET_SELECTED_SECTION_INDEX);
					}, 100);
					if (typeof oldDebouncerFn === 'function') {
						scrollableSection.removeEventListener(
							'scroll',
							oldDebouncerFn
						);
						oldDebouncerFn.cancel();
					}
					requestAnimationFrame(() => {
						scrollableSection.addEventListener(
							'scroll',
							debounceFn
						);
						scrollableSection[propScrollDebouncer] = debounceFn;
						scrollableSection.scrollTop = scrollTop;
					});
				}
			}
		}
	}

	focusFirstElement(sections, fields) {
		for (const section of sections) {
			if (section.visible !== false && section.rows) {
				for (const row of section.rows) {
					for (const column of row) {
						for (const field of column.fields) {
							const fieldAttributes = fields[field];
							if (
								fieldAttributes &&
								fieldAttributes.readonly === false
							) {
								fields[field].autofocus = true;
								return;
							}
						}
					}
				}
			}
		}
	}

	render() {
		const ActivityComboElement = DependencyManager.getElementTag(
			'now-activity-combo'
		);
		const {
			properties: {
				table,
				sysId,
				fields,
				sections,
				config = {},
				serializedChanges,
				encodedRecord,
				activityStream,
				active,
				view,
				isReloading,
				formLayoutConfig = {},
				classicForm,
				inUiBuilder,
				userSession
			},
			headerBackground,
			dispatch
		} = this.props;

		const { isResizing } = this.state;

		if (!sections || !fields)
			return inUiBuilder ? (
				<EmptyStateElement message={t('No data to display')} />
			) : null;

		if (isReloading) return null;

		const { isMissingWorkspaceView, isNewRecord } = config;

		const { isSectionMenuHidden, disableCollapse } = classicForm
			? config
			: this.props.properties;

		const visibleSections = sections.filter(
			section => section.visible !== false
		);
		const instanceId = this.props.componentId;
		const leftColumnClasses = classNames(
			this.leftColumn && this.leftColumn.className
				? this.leftColumn.className.split(' ')
				: '',
			{
				'sn-form-column-layout-col': true,
				[IS_HIDDEN_CLASS_NAME]: this.shouldHideComponent(
					LAYOUT_COMPONENTS.FORM
				)
			}
		);
		const resizeBarClasses = classNames(
			this.resizeBar && this.resizeBar.className
				? this.resizeBar.className.split(' ')
				: '',
			{
				'column-resize-bar': true,
				[IS_HIDDEN_CLASS_NAME]: this.shouldHideComponent(
					LAYOUT_COMPONENTS.RESIZE_BAR
				)
			}
		);

		const rightColumnClasses = classNames(
			['sn-form-column-layout-col', '-activity-stream'],
			{
				[IS_HIDDEN_CLASS_NAME]: this.shouldHideComponent(
					LAYOUT_COMPONENTS.ACTIVITY
				)
			}
		);

		const columnContainerClasses = classNames(
			['sn-form-column-layout-container'],
			{
				'ws-form': !inUiBuilder
			}
		);

		const columnLayoutClasses = classNames(['sn-form-column-layout'], {
			'ws-form': !inUiBuilder
		});

		const leftColumnStyle = {
			flexBasis: `${formLayoutConfig.formSizeRatio ||
				(this.selectedLayoutType === LAYOUT_TYPE.HALF ? 50 : 100)}%`
		};

		const columnLayoutSectionClasses = classNames(['sn-form-column-layout-sections'], {
			'no-section-menu-padding': inUiBuilder && (isSectionMenuHidden || !visibleSections || visibleSections.length < 2)
		});

		function getFieldsForSection(fields, section) {
			const sectionFields = {};
			if (section.rows) {
				section.rows.map((row, index) => {
					row.map((column, index) => {
						column.fields.map(fieldName => {
							sectionFields[fieldName] = fields[fieldName];
						});
					});
				});
			} else {
				const variableFields = _.get(fields, 'variables.variables');
				variableFields &&
					variableFields.map(variable => {
						sectionFields[variable.name] = fields[variable.name];
					});
			}
			return sectionFields;
		}

		return (
			<div className={columnContainerClasses}>
				{isMissingWorkspaceView && renderReadonlyNotification()}

				<div className={columnLayoutClasses} ref={this.containerRef}>
					{this.shouldRenderComponent(LAYOUT_COMPONENTS.FORM) && (
						<div
							className={leftColumnClasses}
							ref={this.leftColumnRef}
							style={leftColumnStyle}
							tabIndex="-1"
						>
							{!isSectionMenuHidden &&
							visibleSections &&
							visibleSections.length > 1 ? (
								<div className="sn-form-column-layout-dropdown">
									<now-dropdown
										items={visibleSections.map((item, idx) => ({
												...item,
												id: idx
											})
										)}
										bare
										hide-caret
										icon="menu-fill"
										select="none"
										size="sm"
										tooltip-content={SECTION_DROPDOWN_ARIA_LABEL}
										config-aria={{
											'aria-label': SECTION_DROPDOWN_ARIA_LABEL
										}}
									/>
								</div>
							) : null}

							<div className={columnLayoutSectionClasses}>
								{sections.map((section, index) => {
									const componentId = `${instanceId}_section_${index}`;
									let props = {
										componentId,
										section,
										fields: getFieldsForSection(
											fields,
											section
										),
										originalFields: fields,
										dispatch,
										disableCollapse,
										serializedChanges,
										encodedRecord,
										isNewRecord,
										table,
										sysId,
										inUiBuilder,
										classicForm,
										headerBackground,
										userSession
									};

									if (useSeismic) {
										props = propsToAttributes(props);
									}
									return (
										<Section
											key={'section-' + index}
											sectionList={sections}
											{...props}
										/>
									);
								})}
							</div>
						</div>
					)}

					{this.shouldRenderComponent(
						LAYOUT_COMPONENTS.RESIZE_BAR
					) && (
						<ResizeBarElement
							resizing={isResizing}
							className={resizeBarClasses}
							ref={this.resizeBarRef}
							tabIndex={ALLOW_KEY_ACCESS_RESIZE_BAR ? '0' : '-1'}
						/>
					)}

					{this.isActivated(LAYOUT_COMPONENTS.ACTIVITY) && (
						<div className={rightColumnClasses} tabIndex="-1">
							<section className="sn-section">
								<div className="sn-section-body">
									<ActivityComboElement
										table={table}
										on-input-change={this.onInputChange}
										fields={fields}
										sys-id={sysId}
										{...activityStream}
										active={active}
										is-new-record={config.isNewRecord}
										view={view}
										is-side-by-side-view-available
									/>
								</div>
							</section>
						</div>
					)}
				</div>
				{config.showActivityStream && (
					<ToolbarElement
						buttons={getLayoutButtons(instanceId)}
						selectedIndex={getSelectedLayoutButtonIndex(
							this.selectedLayoutType
						)}
						onButtonClick={this.toolbarButtonClick}
					/>
				)}
			</div>
		);
	}
}

ColumnLayout.propTypes = {
	table: PropTypes.string,
	sysId: PropTypes.string,
	sections: PropTypes.array,
	fields: PropTypes.object,
	config: PropTypes.object,
	activityStream: PropTypes.object,
	active: PropTypes.bool,
	view: PropTypes.string,
	hasErrorMessage: PropTypes.bool,
	isReloading: PropTypes.bool,
	formLayoutConfig: PropTypes.object,
	serializedChanges: PropTypes.string,
	encodedRecord: PropTypes.string,
	classicForm: PropTypes.bool,
	inUiBuilder: PropTypes.bool,
	gFrom: PropTypes.object,
	userSession: PropTypes.object
};

export default ColumnLayout;
