import { default as ColumnLayoutElement } from '../form/layout/ColumnLayoutElement';
import { RelatedItemElement } from '@servicenow/now-record-common';
import { transformer } from '@devsnc/sn-declarative-actions';
import { WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS } from './WorkspaceFormLayoutConst';
import _ from 'lodash';
import { t } from 'sn-translate';
import { loadActionComponents } from '@devsnc/library-form';

import * as DependencyManager from '../../config/DependencyManager';
const relatedListComponent = DependencyManager.getElementTag(
	'now-record-list-connected-related'
);

const getRelatedListComponentId = (componentIdPrefix, index) => {
	return `${componentIdPrefix}_form_related_list_${index}`;
};

const getDetailedTab = props => {
	return {
		tab: {
			title: t('Details'),
			key: 'Details'
		},
		content: {
			isSeismic: true,
			component: ColumnLayoutElement,
			props
		}
	};
};

const getRelatedItemTabs = (relatedItems = [], props) => {
	// get the related items
	const transformedActions = transformer(relatedItems, props.formModel) || [];
	loadActionComponents(transformedActions);

	return transformedActions.map(action => {
		return {
			tab: {
				title: action.label,
				key: action.assignmentId
			},
			content: {
				isSeismic: true,
				component: RelatedItemElement,
				props: {
					componentName: action.actionComponent,
					actionAttributes: action.actionAttributes,
					...props
				}
			}
		};
	});
};

const getRelatedListTabs = (relatedLists = [], props) => {
	// add related lists
	return relatedLists
		.filter(relatedList => relatedList.visible)
		.map((relatedList, index) => {
			return {
				tab: {
					title: relatedList.displayName,
					count: relatedList.count,
					key: relatedList.value
				},
				content: {
					isSeismic: true,
					component: relatedListComponent,
					props: _.merge({}, props, relatedList.props)
				}
			};
		});
};

const payloadForType = (type, payload) => {
	const options = {};
	switch (type) {
		case WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_QUERY_UPDATED:
			options.query = _.get(payload, 'query');
			break;
		case WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_LIMIT_UPDATED:
			options.limit = _.get(payload, 'limit');
			break;
		case WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_PAGE_UPDATED:
			options.page = _.get(payload, 'page');
			break;
	}
	return options;
};

const changedRelatedListForType = (type, payload, relatedList) => {
	if (
		type === WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_DATA_FETCH_COMPLETE
	) {
		return {
			...relatedList,
			count: _.get(payload, 'count', 0)
		};
	}

	const changedOption = payloadForType(type, payload);
	return {
		...relatedList,
		...changedOption,
		props: {
			...relatedList.props,
			...changedOption
		}
	};
};

const findRelatedListIndex = (relatedLists, relatedListId) => {
	return relatedLists.findIndex(
		relatedList =>
			relatedList.props.componentId === relatedListId ||
			relatedListId.startsWith(`${relatedList.props.componentId}-`)
	);
};

export const getInitRelatedLists = (componentIdPrefix, props) => {
	const {
		relatedLists,
		workspaceConfigId,
		sysId,
		config: { readonly, isNewRecord }
	} = props;
	return (relatedLists || []).map((relatedList, index) => {
		return {
			props: {
				componentId: getRelatedListComponentId(
					componentIdPrefix,
					index
				),
				table: relatedList.table,
				listTitle: relatedList.displayName,
				parentTable: relatedList.parentTable,
				parentRecordSysId: sysId,
				isParentReadOnly: readonly,
				isParentNewRecord: isNewRecord,
				relatedListName: relatedList.value,
				query: _.get(relatedList, 'query', relatedList.filter),
				page: _.get(relatedList, 'page', 1),
				...(relatedList.limit !== undefined ? { limit: relatedList.limit } : {}),
				listType: 'RELATED',
				workspaceConfigId,
				refreshRequested: relatedList.refreshRequested
			},
			...relatedList
		};
	});
};

export const getUpdatedRelatedList = ({
	relatedLists,
	relatedListId,
	payload,
	type
}) => {
	const changedIndex = findRelatedListIndex(relatedLists, relatedListId);
	if (changedIndex < 0) return { relatedLists };

	const changedRelatedList = changedRelatedListForType(
		type,
		payload,
		relatedLists[changedIndex]
	);
	
	const updatedRelatedLists = relatedLists.map((relatedList, index) => ({
		...(index === changedIndex ? changedRelatedList : relatedList)
	}));

	const updatedRelatedList = _.omit(changedRelatedList, ['props']);
	if (_.isEqual(updatedRelatedList, _.omit(relatedLists[changedIndex], ['props'])))
		return { relatedLists: updatedRelatedLists };

	return {
		relatedLists: updatedRelatedLists,
		updatedRelatedList
	};
};

export const isAttachmentInRelatedItems = (formModel, relatedItems) => {
	const transformedItems = transformer(relatedItems, formModel) || [];
	return transformedItems.some(
		action =>
			action.actionComponent === 'now-record-common-attachments-connected'
	);
};

export const getWorkspaceFormTabs = (
	componentIdPrefix,
	relatedLists,
	newProps
) => {
	const {
		table,
		sysId,
		fields,
		sections,
		config = {},
		formLayoutConfig,
		formModal,
		view,
		query,
		activityStream,
		active,
		relatedItems,
		currentUser,
		wrapListText,
		relatedListLimit,
		hideDetails,
		workspaceConfigId,
		formTabs,
		hasErrorMessage,
		isReloading,
		isAttachmentUploadDisabled,
		serializedChanges,
		encodedRecord,
		classicForm
	} = newProps;
	const detailTab = hideDetails
		? undefined
		: getDetailedTab({
				componentId: `${componentIdPrefix}_column_layout`,
				table,
				sysId,
				query,
				fields,
				sections,
				config,
				serializedChanges,
				encodedRecord,
				formModal,
				activityStream,
				active,
				view,
				hasErrorMessage,
				isReloading,
				formLayoutConfig,
				classicForm
		  });

	const {
		readonly = false,
		isNewRecord = false,
		canReadRecord = true
	} = config;

	const formModel = {
		view,
		table,
		sysId,
		query,
		formModal, // TODO: remove this
		currentUser,
		isNewRecord,
		isReadOnly: readonly,
		canReadRecord,
		fields,
		isAttachmentUploadDisabled,
		serializedChanges,
		encodedRecord,
		classicForm
	};

	const relatedItemTabs = getRelatedItemTabs(relatedItems, {
		formModel
	});

	const relatedListTabs = getRelatedListTabs(relatedLists, {
		view,
		active,
		limit: relatedListLimit,
		wordWrap: wrapListText,
		hideNew: _.get(config, 'readonly', false),
		workspaceConfigId,
		...(isReloading ? { refreshRequested: { timestamp: Date.now() } } : {}),
		...formModel
	});

	const tabs = [
		...(detailTab ? [detailTab] : []),
		...relatedItemTabs,
		...relatedListTabs
	];

	const tabsOrder = _.get(formTabs, 'isDefaultTabOrder', true)
		? []
		: _.get(formTabs, 'tabsOrder', []);

	const maxLength = Math.max(tabs.length, tabsOrder.length);
	const sortedTabs = _.sortBy(tabs, item => {
		const foundIndex = tabsOrder.findIndex(tab => {
			return tab.value === item.tab.key;
		});
		return foundIndex !== -1 ? foundIndex : maxLength;
	});

	return sortedTabs;
};
