import { React, PureComponent, PropTypes } from '../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import { t } from 'sn-translate';
import { keyUtils } from '@devsnc/library-form';
import Toggle from './Toggle';
import '@servicenow/now-dropdown';
import '@servicenow/now-tooltip';
import classNames from 'classnames/dedupe';
import { GFORM_ACTIONS } from '@devsnc/sn-scripting-environment';
import { nodeContainsFocus } from '../../utils/dom';
import {
	isTransparent,
	rgb2hex
} from '../../utils/style';

const TOGGLE_BUTTONS = {
	expand: {
		buttonClass: 'sn-section-toggle is-open',
		ariaLabel: t('Close Section'),
		imageClass: 'sn-section-toggle is-open',
		imageType: 'chevronUpOutline'
	},
	collapse: {
		buttonClass: 'sn-section-toggle is-collapsed',
		ariaLabel: t('Open Section'),
		imageClass: 'sn-section-toggle is-collapsed',
		imageType: 'chevronUpOutline'
	}
};

const setAriaStateMessages = label => ({
	true: t(`The content of the ${label} section is now visible`),
	false: t(`The content of the ${label} section is now hidden`)
});

class FormSectionHeader extends PureComponent {
	constructor(props) {
		super(props);
		this.toggleSection = this.toggleSection.bind(this);
		this.state = {
			divRef: undefined,
			ariaVisibleStateMessages: setAriaStateMessages(props.label)
		};
	}

	getParentRefs(divRef) {
		return {
			headerRef: divRef.parentElement,
			sectionRef: divRef.parentElement
				? divRef.parentElement.parentElement
				: null
		};
	}

	componentWillReceiveProps({ isVisible }) {
		if (isVisible !== undefined && isVisible !== this.props.isVisible) {
			if (!isVisible) {
				const { headerRef, sectionRef } = this.getParentRefs(
					this.state.divRef
				);
				if (!sectionRef || !headerRef) return;
				// if a script has closed the section the user is already in, once closed refocus to top of header
				if (nodeContainsFocus(sectionRef)) headerRef.focus();
			}
		}
	}

	refDiv = node => {
		if (node) this.setState({ divRef: node });
	};

	getToggleAttributes() {
		return this.props.isVisible
			? TOGGLE_BUTTONS.collapse
			: TOGGLE_BUTTONS.expand;
	}

	toggleSection(evt) {
		if (!this.props.disableCollapse) {
			if (evt.type === 'keypress') {
				if (!keyUtils.onEnter(evt) && !keyUtils.onSpace(evt)) {
					return;
				}
				evt.preventDefault();
			}
			if (this.props.dispatch)
				this.props.dispatch(
					GFORM_ACTIONS.GFORM_SECTION_EXPANDED_STATE_CHANGED,
					{
						sectionName: this.props.caption,
						expanded: !this.props.isVisible
					}
				);
		}
	}

	render() {
		let toggle = this.getToggleAttributes();
		const { ariaVisibleStateMessages } = this.state;
		const { label, isVisible, disableCollapse, headerBackground, children } = this.props;
		const headerBackgroundIsTransparent = isTransparent(headerBackground);
		const headerStyle = headerBackground && !headerBackgroundIsTransparent ? 
		{
			"--header-background-opaque": `${rgb2hex(headerBackground)}ff`,
			"--header-background-transparent": `${rgb2hex(headerBackground)}00`
		} : {};
		

		return (
			<React.Fragment>
				<header
					className={`${classNames('sn-section-header', {
						collapsable: !disableCollapse,
						"-default-header-background": headerBackgroundIsTransparent
					})}`}
					style={headerStyle}
					onClick={this.toggleSection}
					onKeyPress={this.toggleSection}
					tabIndex="0"
				>
					<div
						className="sn-section-header-content"
						ref={this.refDiv}
					>
						<span>{label}</span>
						<now-tooltip
							target-ref={this.state.divRef}
							content={label}
						/>
					</div>
					{!disableCollapse && (
						<div
							className="sn-section-header-content -static"
							aria-expanded={isVisible}
							role="button"
							aria-label={label}
						>
							<now-icon
								icon={toggle.imageType}
								className={toggle.imageClass}
								size="md"
							/>
						</div>
					)}
					<span
						className="collapsed-state-message"
						aria-live="polite"
					>
						{ariaVisibleStateMessages[isVisible]}
					</span>
				</header>
				<Toggle
					isVisible={
						disableCollapse || (!disableCollapse && isVisible)
					}
					children={children}
				/>
			</React.Fragment>
		);
	}
}

FormSectionHeader.propTypes = {
	caption: PropTypes.string,
	label: PropTypes.string,
	sectionList: PropTypes.array,
	disableCollapse: PropTypes.bool,
	isVisible: PropTypes.bool,
	headerBackground: PropTypes.string,
	dispatch: PropTypes.func
};

FormSectionHeader.defaultProps = {
	disableCollapse: false
};

export default FormSectionHeader;
