import {
	Component,
	PropTypes,
	getInstanceId,
	React,
	propsToAttributes
} from '../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import '@devsnc/sn-glide-form-controls';
import * as DependencyManager from '../../config/DependencyManager';
// React components
import { default as HeaderLayoutElement } from './layout/HeaderLayoutElement';
import { default as FormBlobElement } from './formBlobElement';
import { default as NotificationsWrapperElement } from '../notifications/NotificationsWrapperElement';
import { t } from 'sn-translate';
import '@servicenow/now-legacy-icon';
import classNames from 'classnames/dedupe';
import { createResizableController } from '../../components/utils/resizableController';
import { ResizeBarElement ,
	getReadOnlyPreviewOverlayProps,
	AttachmentOverlayElement,
	isAttachmentInSidebar
} from '@servicenow/now-record-common';
import {
	WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS as ACTIONS
} from './WorkspaceFormLayoutConst';
import { LAYOUT_TYPE } from '../form/layout/formLayoutHelper';
import { FORM_SIDEBAR_RESIZED } from './WorkspaceFormConstants';

import { isAttachmentInRelatedItems } from './formTabUtils';

import {
	MIN_LEFT_COLUMN_WIDTH,
	MIN_RIGHT_COLUMN_WIDTH
} from '../../components/form/layout/layoutConstants';

const SIDEBAR_PANEL_MIN_WIDTH = 370;

const ALLOW_KEY_ACCESS_RESIZE_BAR = false;
const NOW_RECORD_COMMON_TEMPLATES_CONNECTED = "now-record-common-templates-connected";

class WorkspaceFormLayout extends Component {
	constructor(props) {
		super(props);

		// bind methods
		this.setModalContainerRef = this.setModalContainerRef.bind(this);
		this.recalculateResizeController = this.recalculateResizeController.bind(
			this
		);
		this.componentIdPrefix = getInstanceId(this.props);
		this.baseInstanceId = this.props.componentId.replace('_env', '');
		this.rootInstanceId = this.props.componentId.split('_form')[0];
		this.resizeKey = 'workspace_form_layout';
	}

	leftColumnRef = ref => {
		if (!ref) return;

		this.leftColumn = ref;
		this.setupResizableWhenReady();
	};

	containerRef = ref => {
		if (!ref) return;

		this.container = ref;
		this.setupResizableWhenReady();
	};

	resizeBarRef = ref => {
		if (!ref) return;

		if (this.resizeBar !== ref) {
			this.resizeBar = ref;
		}
	};

	setupResizableWhenReady = () => {
		if (!this.container || !this.leftColumn) return;

		if (!this.resizeBar) {
			this.leftColumn.style.flexBasis = '100%';
			return;
		}

		if (this.resizableController) this.resizableController.destroy();

		const sidebarExpanded = this.props.sidebarExpanded;
		const containerWidth = this.container.getBoundingClientRect().width;
		const minLeftColumnWidth =
			this.props.properties.leftColumnMinWidth ||
			MIN_LEFT_COLUMN_WIDTH + MIN_RIGHT_COLUMN_WIDTH;
		const minRightColumnWidth = SIDEBAR_PANEL_MIN_WIDTH;

		const minBasis = containerWidth
			? (minLeftColumnWidth / containerWidth) * 100
			: 0;
		const maxBasis =
			((containerWidth - minRightColumnWidth) / containerWidth) * 100;
		const { formLayoutConfig } = this.props.properties;
		let startingBasis = 100 - formLayoutConfig.sideBarSizeRatio;

		if (startingBasis > maxBasis) {
			startingBasis = maxBasis;
		} else if (startingBasis < minBasis) {
			startingBasis = minBasis;
		}

		const initialBasis = sidebarExpanded ? startingBasis : 100;
		this.currentBasis = startingBasis;
		this.resizableController = createResizableController(
			this.container,
			this.leftColumn,
			this.resizeBar,
			this.resizeKey,
			{
				allowKeyAccess: ALLOW_KEY_ACCESS_RESIZE_BAR,
				minLeftColumnWidth,
				minRightColumnWidth,
				beforeStartResizing: () => {
					this.props.dispatch(ACTIONS.UPDATE_STATE, {
						isResizing: true
					});
				},
				afterStopResizing: sizeRatio => {
					const sideBarSizeRatio = 100 - sizeRatio;
					this.props.dispatch(FORM_SIDEBAR_RESIZED, {
						table: this.props.properties.table,
						sideBarSizeRatio: sideBarSizeRatio
					});
					this.currentBasis = sizeRatio;
					this.resizeBar.setAttribute('aria-valuenow', sizeRatio);
					this.props.dispatch(ACTIONS.UPDATE_STATE, {
						isResizing: false
					});
				}
			}
		);

		this.resizeBar.setAttribute('aria-valuenow', initialBasis);
		this.resizeBar.setAttribute(
			'aria-valuemin',
			(minLeftColumnWidth / containerWidth) * 100
		);
		this.resizeBar.setAttribute('aria-valuemax', 100);

		this.resizableController.initialize();
		this.resizableController.setWidth(initialBasis);
		if (sidebarExpanded) {
			this.resizableController.enableResizing();
		}
	};

	updateBasis(sidebarExpanded) {
		if (!this.leftColumn) return;
		if (!this.currentBasis) return;

		const currentBasis = sidebarExpanded ? this.currentBasis : 100;
		this.resizeBar.setAttribute('aria-valuenow', currentBasis);
		this.resizableController.setWidth(currentBasis);
	}

	recalculateResizeController() {
		this.setupResizableWhenReady();
	}

	componentDidMount() {
		window.addEventListener('resize', this.recalculateResizeController);
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.recalculateResizeController);
	}

	shouldComponentUpdate(nextProps) {
		if (
			nextProps.properties.userPreferences !==
			this.props.properties.userPreferences
		) {
			const prevShowSidebar =
				(
					this.props.properties.userPreferences.find(
						x => x.name === 'workspace.showAgentAssist'
					) || {}
				).value === 'true';
			const newShowSidebar =
				(
					nextProps.properties.userPreferences.find(
						x => x.name === 'workspace.showAgentAssist'
					) || {}
				).value === 'true';
			if (prevShowSidebar !== newShowSidebar) {
				this.props.dispatch(ACTIONS.UPDATE_STATE, {
					sidebarExpanded: newShowSidebar
				});
				return false;
			}
		}

		if (nextProps.sidebarExpanded !== this.props.sidebarExpanded)
			this.updateBasis(nextProps.sidebarExpanded);

		return (
			!_.isEqual(
				_.omit(nextProps.properties, ['handleUiActionClick']),
				_.omit(this.props.properties, ['handleUiActionClick'])
			) ||
			nextProps.showAttachmentPreview !==
				this.props.showAttachmentPreview ||
			nextProps.loadDocViewerIfNecessary !== this.props.loadDocViewerIfNecessary ||
			nextProps.sidebarExpanded !== this.props.sidebarExpanded ||
			nextProps.isModalOpen !== this.props.isModalOpen
		);
	}

	componentDidUpdate(prevProps) {
		if (
			prevProps.properties.selectedLayoutType !==
			this.props.properties.selectedLayoutType
		)
			this.setupResizableWhenReady();

		if (prevProps.isResizing === true && this.props.isResizing === false)
			this.setupResizableWhenReady();

		if (
			this.props.properties.sidebarActions &&
			this.props.properties.sidebarActions.length === 0 &&
			prevProps.properties.sidebarActions &&
			prevProps.properties.sidebarActions.length > 0
		) {
			this.props.dispatch(ACTIONS.UPDATE_STATE, {
				sidebarExpanded: false
			});
		}
	}

	/**
	 * Utility method to check if the current prop is the same as the incoming prop change
	 * @param changes
	 * @param prop
	 * @returns {boolean}
	 */
	isPropDifferent(changes, prop) {
		if (changes.hasOwnProperty(prop))
			return this.props[prop] !== changes[prop];
		return (
			changes.properties.hasOwnProperty(prop) &&
			this.props.properties[prop] !== changes.properties[prop]
		);
	}

	setModalContainerRef(node) {
		const {
			properties: { formModal },
			componentId
		} = this.props;
		if (this.formModalContainer !== node) {
			this.formModalContainer = node;
			if (formModal)
				formModal.setContainer(this.formModalContainer, componentId);
		}
	}

	render() {
		const {
			properties: {
				table,
				sysId,
				dehydrate,
				isLoadingRecord,
				isReloading,
				notifications
			},
			isModalOpen
		} = this.props;

		if (dehydrate) return null;

		if (isLoadingRecord)
			return this.renderFormOverlay(t('Loading record...'));

		return (
			<form
				className="sn-workspace-form-layout"
				aria-labelledby={`${this.componentIdPrefix}_label`}
			>
				{isReloading && this.renderFormOverlay(t('Loading record...'))}

				<NotificationsWrapperElement
					component-id={`${
						this.componentIdPrefix
					}_notifications_wrapper`}
					table={table}
					sys-id={sysId}
					notifications={notifications}
					aria-hidden={isModalOpen ? "true" : null}
				/>
				{!dehydrate && this.renderFormContents(this.componentIdPrefix)}
			</form>
		);
	}

	renderFormContents(componentId) {
		const {
			properties: {
				view,
				table,
				sysId,
				query,
				config,
				handleUiActionClick,
				actionNodes,
				fixedButtonNumber,
				isAttachmentUploadDisabled,
				serializedChanges,
				encodedRecord,
				showRibbon,
				ribbon,
				fields,
				sidebarActions,
				relatedItems,
				formHeader = {},
				recordTags,
				active,
				specialHandlingNote,
				formModal,
				currentUser,
				isLoadingRecord,
				formLayoutConfig,
				hideDetails,
				disableCollapse,
				defaultTab,
				workspaceConfigId,
				sections,
				formTabs,
				selectedTabIndex,
				selectedTabName,
				activityStream,
				relatedLists,
				notifications,
				selectedLayoutType,
				isReloading,
				classicForm,
				templateRefresh
			},
			sidebarExpanded,
			isModalOpen,
			isResizing,
			disableForm,
			dispatch,
			showAttachmentPreview,
			attachmentPreviewItem,
			loadDocViewerIfNecessary
		} = this.props;
		const {
			readonly,
			isNewRecord,
			canReadRecord,
			isValidRecord,
			isScriptableTable,
			lastErrorMessage
		} = config;
		const headerLayoutAttributes = propsToAttributes({
			componentId: `${componentId}_header_layout`,
			formHeader,
			isReadonly: readonly,
			classicForm,
			hideUiActions: hideDetails,
			sysId,
			table,
			recordTags,
			showRibbon,
			ribbon,
			labelId: `${componentId}_label`,
			active,
			workspaceConfigId,
			handleClick: handleUiActionClick,
			actionNodes,
			fixedButtonNumber,
			overlayContainer: '.sn-workspace-form-layout'
		});

		if (isValidRecord === false) {
			// sys_id couldn't be found
			const message = isScriptableTable
				? lastErrorMessage
				: t('Record not found');
			return (
				<sn-annotation important="true" className="sn-form-annotation">
					{message}
				</sn-annotation>
			);
		} else if (canReadRecord === false) {
			// ACL says no
			return (
				<sn-annotation important="true" className="sn-form-annotation">
					{t('Security constraints prevent access to requested page')}
				</sn-annotation>
			);
		}
		const ShnModalElement = DependencyManager.getElementTag(
			DependencyManager.ELEMENT_TAGS.SN_COMPONENT_SHN_MODAL
		);

		const actions = sidebarActions || [];

		const formModel = {
			view,
			table,
			sysId,
			query,
			isNewRecord,
			canReadRecord,
			formModal,
			isReadOnly: readonly,
			currentUser,
			fields,
			isAttachmentUploadDisabled,
			serializedChanges,
			encodedRecord
		};

		const hasErrorMessage =
			notifications.findIndex(n => n.type === 'error') > -1;

		const formBlobAttrs = {
			table,
			sysId,
			view,
			query,
			workspaceConfigId,
			defaultTab,
			fields,
			sections,
			config,
			formLayoutConfig,
			formModal,
			activityStream,
			active,
			relatedItems,
			currentUser,
			hideDetails,
			disableCollapse,
			hasErrorMessage,
			formTabs,
			relatedLists,
			selectedTabIndex,
			selectedTabName,
			isReloading,
			classicForm,
			isAttachmentUploadDisabled,
			serializedChanges,
			encodedRecord
		};

		const isAttachmentPresentInSidebar = isAttachmentInSidebar(
			formModel,
			actions
		);
		const isAttachmentPresentInRelatedItems = isAttachmentInRelatedItems(
			formModel,
			relatedItems
		);

		return (
			<React.Fragment>
				<div ref={this.setModalContainerRef} />
				{!isAttachmentPresentInSidebar &&
				!isAttachmentPresentInRelatedItems
					? this.renderReadOnlyAttachmentOverlay()
					: null}
				{specialHandlingNote.enabled ? (
					<ShnModalElement
						component-id={`${componentId}_shn`}
						table={table}
						sys-id={sysId}
					/>
				) : null}
				<HeaderLayoutElement {...headerLayoutAttributes} aria-hidden={isModalOpen ? "true" : null} />
				<section
					className="sn-workspace-form-layout-contents"
					tabIndex="-1"
					ref={this.containerRef}
					aria-hidden={isModalOpen ? "true" : null}
				>
					{
						disableForm && !isReloading && this.renderFormOverlay()
					}
					<div
						ref={this.leftColumnRef}
						className={
							selectedLayoutType === LAYOUT_TYPE.HALF
								? 'form-blob-flex-width'
								: 'form-blob-half-width'
						}
					>
						<FormBlobElement
							{...formBlobAttrs}
							tab-content-extras={
								isAttachmentPresentInRelatedItems
									? {
											'now-record-common-attachments-connected': {
												overlayContainer:
													'.sn-workspace-form-layout',
												showAttachmentPreview,
												attachmentPreviewItem,
												loadDocViewerIfNecessary,
												...(isReloading
													? {
															refreshRequested: {
																timestamp: Date.now()
															}
													  }
													: {})
											}
									  }
									: {}
							}
							component-id={`${componentId}_formblob`}
						/>
					</div>

					{!isLoadingRecord && !_.isEmpty(actions) && (
						<React.Fragment>
							<ResizeBarElement
								ref={this.resizeBarRef}
								active={sidebarExpanded}
								resizing={isResizing}
								className={sidebarExpanded ? '' : 'is-hidden'}
								color="dark"
							/>
							<aside
								className={classNames(
									'sn-workspace-form-layout-contents-aside',
									{ expanded: sidebarExpanded },
									{ 'aside-min-width': sidebarExpanded }
								)}
								id={`${componentId}_sidebar`}
							>
								<now-record-common-sidebar
									form-model={formModel}
									actions={actions}
									opened={sidebarExpanded}
									component-id={`${componentId}_sidebar`}
									manage-opened
									classic-form={classicForm}
									sidebar-panel-extras={{
											...(!isAttachmentPresentInRelatedItems ?
												{
													'now-record-common-attachments-connected': {
														overlayContainer:
															'.sn-workspace-form-layout',
														showAttachmentPreview,
														attachmentPreviewItem,
														loadDocViewerIfNecessary,
														...(isReloading
															? {
																	refreshRequested: {
																		timestamp: Date.now()
																	}
															  }
															: {})
													}
												} : {}),
											...(templateRefresh ?
												{
													[NOW_RECORD_COMMON_TEMPLATES_CONNECTED]: {
														refreshRequested: {
															timestamp: templateRefresh
														}
													}
												} : {})
									}}
									record-display-value={
										formHeader.recordDisplayValue
									}
								/>
							</aside>
						</React.Fragment>
					)}
				</section>
			</React.Fragment>
		);
	}

	renderFormOverlay(localizedMessage = t('Please wait...')) {
		return (
			<div className="sn-workspace-form-layout-overlay">
				<now-icon icon="loader-outline" size="lg" spin />
				<span>{localizedMessage}</span>
			</div>
		);
	}

	renderReadOnlyAttachmentOverlay() {
		const props = getReadOnlyPreviewOverlayProps(
			this.props,
			this.props.properties
		);
		return <AttachmentOverlayElement {...props} />;
	}
}

WorkspaceFormLayout.propTypes = {
	hideDetails: PropTypes.bool,
	defaultTab: PropTypes.string,
	isLoadingRecord: PropTypes.bool,
	isReloading: PropTypes.bool,
	classicForm: PropTypes.bool,
	table: PropTypes.string,
	sysId: PropTypes.string,
	view: PropTypes.string,
	config: PropTypes.object,
	handleUiActionClick: PropTypes.func,
	actionNodes: PropTypes.array,
	fixedButtonNumber: PropTypes.number,
	fields: PropTypes.object,
	sections: PropTypes.array,
	relatedLists: PropTypes.array,
	relatedItems: PropTypes.array,
	sidebarActions: PropTypes.array,
	recordTags: PropTypes.object,
	attachments: PropTypes.object,
	formModal: PropTypes.object,
	showRibbon: PropTypes.bool,
	showSidebar: PropTypes.bool,
	activityStream: PropTypes.object,
	currentUser: PropTypes.object,
	formHeader: PropTypes.object,
	ribbon: PropTypes.object,
	active: PropTypes.bool,
	dehydrate: PropTypes.bool,
	specialHandlingNote: PropTypes.object,
	workspaceConfigId: PropTypes.string,
	notifications: PropTypes.array,
	formTabs: PropTypes.object,
	selectedTabIndex: PropTypes.number,
	formSettings: PropTypes.object,
	selectedLayoutType: PropTypes.number,
	leftColumnMinWidth: PropTypes.number,
	formLayoutConfig: PropTypes.object,
	selectedTabName: PropTypes.string,
	templateRefresh: PropTypes.object
};

export default WorkspaceFormLayout;
