import { React } from '../../utils/seismic';
import styles from './WorkspaceQuickFormLayout.scss';
import '@devsnc/sn-glide-form-controls';
import * as DependencyManager from '../../../config/DependencyManager';
import { default as ColumnLayoutElement } from '../../form/layout/ColumnLayoutElement';
import { default as TabsElement } from '../../tabs/TabsElement';
import { t } from 'sn-translate';
import { forEach, isArray } from 'lodash';
import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-button';
import '@servicenow/now-legacy-icon';
import '../../form/header/SimpleFormHeader';
import '../../form/footer/SimpleFormFooter';
import {
	QUICK_FORM_INTERNAL_ACTIONS,
	QUICK_MODAL_PROPS
} from './WorkspaceQuickFormConstants';
import { QUICK_FORM_ACTIONS } from '../../../constants/actions';
import actions from './WorkspaceQuickFormLayoutActions';
import { getElementName } from '../../utils/seismic';
import '@servicenow/now-modal';

const CONFIRM_BUTTON_TITLE = 'Update';

const onActivityStreamInputChange = function(changedFields, state) {
	if (!isArray(changedFields)) return;

	const { gForm } = state.properties;
	if (!gForm) throw 'Error: gForm is null, cannot set value';

	forEach(changedFields, fieldObject => {
		const { field, value, displayValue } = fieldObject;
		gForm.setValue(field, value, displayValue);
	});
};

const setModalContainerRef = function(node, state, updateState) {
	const {
		properties: { formModal },
		formModalContainer,
		componentId
	} = state;
	if (formModalContainer !== node) {
		updateState({
			formModalContainer: node
		});
		if (formModal) {
			formModal.setContainer(node, componentId);
		}
	}
};

const renderFormDefaultMessage = function() {
	return (
		<div className="sn-workspace-form-layout-default-message">
			<span>
				{t(
					"If information doesn't appear on this tab, it may be hidden on purpose. Please contact your system administrator for more information."
				)}
			</span>
		</div>
	);
};

const renderFormOverlay = function(localizedMessage = t('Please wait...')) {
	return (
		<div className="sn-workspace-form-layout-overlay">
			<now-icon icon="loader-outline" size="lg" spin />
			<span>{localizedMessage}</span>
		</div>
	);
};

const getWorkspaceFormTabs = function(state) {
	const {
		properties: {
			table,
			sysId,
			fields,
			sections,
			config,
			gForm,
			formModal,
			query,
			activityStream: {
				events,
				filterOptions,
				timestamp,
				userReferences,
				supplemental,
				ambChannels
			},
			view,
			serializedChanges,
			encodedRecord,
			classicForm
		},
		componentId: instanceId
	} = state;

	const activityComboElement = DependencyManager.getElementTag(
		'now-activity-combo'
	);

	let workspaceFormTabs = [
		{
			tab: {
				title: t('Details'),
				key: 'Details'
			},
			content: {
				isSeismic: true,
				component: ColumnLayoutElement,
				props: {
					componentId: `${instanceId}_column_layout`,
					table,
					sysId,
					query,
					fields,
					sections,
					config: { ...config, showActivityStream: false },
					gForm, //TODO: remove this
					formModal,
					serializedChanges,
					encodedRecord,
					classicForm
				}
			}
		}
	];

	if (config.showActivityStream)
		workspaceFormTabs.push({
			tab: {
				title: t('Activity'),
				key: 'Activity'
			},
			content: {
				isSeismic: true,
				component: activityComboElement,
				props: {
					componentId: `${instanceId}_activity_stream`,
					table,
					sysId,
					query,
					fields,
					events: events || [],
					supplemental: supplemental || [],
					ambChannels: ambChannels || [],
					userReferences: userReferences || [],
					filterOptions: filterOptions || [],
					timestamp: timestamp || new Date(),
					isNewRecord: config ? config.isNewRecord : false,
					onInputChange: arg => {
						onActivityStreamInputChange(arg, state);
					},
					view
				}
			}
		});

	return workspaceFormTabs;
};

const renderFormContents = function(state, dispatch, updateState) {
	const {
		componentId,
		showFooter,
		selectedTabIndex,
		properties: {
			config,
			formHeader: { primaryValue } = {},
			isDirty,
			formSettings: { isDetailsHidden = false } = {},
			classicForm
		}
	} = state;

	if (!config) return renderFormOverlay();

	const {
		canReadRecord,
		isValidRecord,
		isScriptableTable,
		lastErrorMessage
	} = config;

	const workspaceFormTabs = getWorkspaceFormTabs(state, dispatch);

	if (isValidRecord === false) {
		const message = isScriptableTable
			? lastErrorMessage
			: t('Record not found');
		// sys_id couldn't be found
		return (
			<sn-annotation important="true" className="sn-form-annotation">
				{message}
			</sn-annotation>
		);
	} else if (canReadRecord === false) {
		// ACL says no
		return (
			<sn-annotation important="true" className="sn-form-annotation">
				{t('Security constraints prevent access to requested page')}
			</sn-annotation>
		);
	}

	return (
		<Fragment>
			<div ref={node => setModalContainerRef(node, state, updateState)} />
			<sn-form-internal-simple-form-header
				title={primaryValue}
				aria-label-close={t('Close Quick Edit Form')}
			/>
			<section className="sn-workspace-form-layout-contents">
				{state.disableForm ? renderFormOverlay() : null}
				{!isDetailsHidden ? (
					<TabsElement
						context="tab"
						context-modifier="full-container"
						className="context-tab context-modifier-full-container"
						tab-options={workspaceFormTabs}
						more-overflow={false}
						show-tooltips={true}
						component-id={`${componentId}_formtabs`}
						selected-tab={selectedTabIndex}
						default-tab="0"
					/>
				) : (
					renderFormDefaultMessage()
				)}
			</section>
			{showFooter ? (
				<sn-form-internal-simple-form-footer
					is-dirty={isDirty}
					confirm-button-title={CONFIRM_BUTTON_TITLE}
				/>
			) : null}
		</Fragment>
	);
};

const WorkspaceQuickFormLayoutElement = getElementName(
	'quick-form-layout',
	'private'
);
createCustomElement(WorkspaceQuickFormLayoutElement, {
	renderer: {
		type: snabbdom,
		view(state, { dispatch, updateState }) {
			const {
				displayModal,
				componentId,
				properties: { isLoadingRecord }
			} = state;

			if (isLoadingRecord)
				return renderFormOverlay(t('Loading record...'));

			return (
				<form
					className="sn-workspace-form-layout"
					aria-labelledby={`${componentId}_label`}
				>
					{renderFormContents(state, dispatch, updateState)}
					<now-modal
						opened={displayModal}
						{...QUICK_MODAL_PROPS}
						size="md"
						nowHoist
					/>
				</form>
			);
		}
	},
	properties: {
		isLoadingRecord: {},
		table: {},
		sysId: {},
		view: {},
		config: {},
		gForm: {}, //TODO: remove this
		uiActions: {},
		fields: {},
		sections: {},
		formModal: {},
		activityStream: {},
		formHeader: {},
		formSettings: {},
		isDirty: {
			default: false,
			onChange(value, previousValue, dispatch) {
				dispatch(QUICK_FORM_ACTIONS.DIRTY_CHANGED, { isDirty: value });
			}
		},
		serializedChanges: {},
		encodedRecord: {},
		classicForm: {}
	},
	initialState: {
		formModalContainer: null,
		showFooter: true,
		displayModal: false,
		selectedTabIndex: 0
	},
	...actions,
	styles
});

export default WorkspaceQuickFormLayoutElement;
