import { createElement } from '../utils/seismic';
import { default as Tabs } from './Tabs';
import styles from './Tabs.scss';
import { executeEffectIfValidEventSource } from '@devsnc/library-form';
import { FORM_TAB_HEADER_EVENT_SOURCE } from '../../constants/eventSources';

export default createElement(
	{
		name: 'Tabs',
		component: Tabs,
		actionHandlers: {
			'NOW_TABS#SELECTED_ITEM_SET': {
				...executeEffectIfValidEventSource(
					FORM_TAB_HEADER_EVENT_SOURCE,
					({ updateState }) =>
						updateState({
							selectedTab: _.get(action, 'payload.value')
						}),
					[
						({ dispatch, action }) =>
							dispatch('TABS#TAB_SELECTED', {
								tabIndex: _.get(action, 'payload.value')
							})
					]
				),
				stopPropagation: true
			},
			'NOW_DROPDOWN#ITEM_CLICKED' : {
				stopPropagation: true
			},
		},
		styles
	},
	'private'
);
