const DIMENSION_RESIZE_CLASS = 'dimension-resize';

export const resizingDimension = resizingEl => {
	let lastSize;
	const resizingPixel = pixel => {
		resizingEl.style.width = pixel + 'px';
		lastSize = pixel;
	};

	const resizingDimension = width => {
		resizingPixel(width);
	};

	const getLastSize = () => lastSize;

	return {
		resizing: resizingDimension,
		resizeClassName: DIMENSION_RESIZE_CLASS,
		resizingWithRawValue: resizingPixel,
		getLastSize
	};
};
