import { createCustomElement } from '@servicenow/ui-core';
import snabbdom, { Fragment } from '@servicenow/ui-renderer-snabbdom';
import styles from './WorkspaceMultiForm.scss';
import { t } from 'sn-translate';
import '../../../components/form/layout/workspaceSimpleForm/WorkspaceSimpleFormElement';
import '../../form/header/SimpleFormHeader';
import '../../form/footer/SimpleFormFooter';
import actions from './workspaceMultiFormActions';
import { MULTI_FORM_INTERNAL_ACTIONS , MULTI_MODAL_PROPS } from './workspaceMultiFormActionsConst';
import '@servicenow/now-button';
import '@servicenow/now-legacy-icon';
import { default as trapActions } from '../common/actions/trapActionsFormEmbeddedMode';
import '@servicenow/now-modal';


const getTitle = ({ title, ids }) => {
	const count = ids.split(',').length;
	return `${title}${count !== 1 ? 's' : ''} (${count})`;
};

const renderFormOverlay = (localizedMessage = t('Please wait...')) => {
	return (
		<div className="sn-workspace-form-layout-overlay">
			<now-icon icon="loader-outline" size="lg" spin />
			<span>{localizedMessage}</span>
		</div>
	);
};

const view = (state, { dispatch }) => {
	const {
		properties: { title, table, view, workspaceConfigId, sysIds },
		displayModal,
		changedFields,
		isDirty
	} = state;

	const handleFormChange = fields => {
		dispatch(MULTI_FORM_INTERNAL_ACTIONS.CHANGE, {
			fields
		});
	};

	const handleFormLoad = ({ loaded }) => {
		dispatch(MULTI_FORM_INTERNAL_ACTIONS.LOADING, {
			loading: !loaded
		});
	};

	return (
		<Fragment>
			<div>{state.loading ? renderFormOverlay() : null}</div>
			<div className="multi-form">
				<sn-form-internal-simple-form-header
					title={t(title)}
					aria-label-close={t('Close Multi Edit Form')}
				/>
				<section className="multi-form-section">
					<sn-form-internal-workspace-simple-form
						table={table}
						view={view}
						sys-ids={sysIds}
						workspace-config-id={workspaceConfigId}
						onDataChanged={handleFormChange}
						onDataLoaded={handleFormLoad}
					/>
				</section>
				<sn-form-internal-simple-form-footer
					is-dirty={isDirty}
					confirm-button-title={t('Update')}
				/>
			</div>
			<now-modal
				opened={displayModal}
				{...MULTI_MODAL_PROPS}
				size="md"
				nowHoist
			/>
		</Fragment>
	);
};

const WorkspaceMultiFormElement = 'sn-record-common-multi-form';

/**
 * ```jsx
 * <sn-record-common-multi-form
 *		sysIds={...}
 *		table={...}
 *		view={...}
 *		workspaceConfigId={...}
 *		/>
 * ```
 *
 * @seismicElement sn-record-common-multi-form
 * @summary This component is used to update multiple records from a list by showing an empty form.\nThe save button will not be enabled until the user makes a change to the form.
 */
createCustomElement(WorkspaceMultiFormElement, {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		displayModal: false,
		loading: true,
		isDirty: false,
		changedFields: {}
	},
	properties: {
		/**
		 * table of the records being displayed
		 *
		 * @type {string}
		 */
		table: {},
		/**
		 * specific view of the tables' configured layout to render
		 * @type {string}
		 */
		view: {},
		/**
		 * ids of the record being displayed
		 * @type {string[]}
		 */
		sysIds: {},
		/**
		 * title - auto generated
		 * @type {string}
		 */
		title: {
			computed({ properties: { sysIds = '' } }) {
				return getTitle({ title: 'Edit item', ids: sysIds });
			}
		},
		/**
		 * id of the workspace the records are being loaded in eg. (agent, hr, vlm)
		 * @type {string}
		 */
		workspaceConfigId: {
			default: '7b24ceae5304130084acddeeff7b12a3' // default value to again workspace to workaround issue of multi-edit form inside list to fallback to use the legacy agentworkspace field decorators.
		}
	},
	actions: {
		...actions.actions,
		...trapActions.actions
	},
	actionHandlers: {
		...actions.actionHandlers,
		...trapActions.actionHandlers
	},
	dispatches: {
		/**
		 * Dispatched when a notification is generated from this component or any of its child components
		 *
		 * @type {AddNotificationsPayload}
		 */
		ADD_NOTIFICATIONS: {},
		/**
		 * Dispatched when a user has closed the multi form by clicking on the 'X' button in the top right
		 */
		'MULTI_FORM#CLOSE': {},
		/**
		 * Dispatched after a user has clicked save and the save operation has succeeded
		 *
		 * @type {MultiFormSaveSuccessPayload}
		 */
		'MULTI_FORM#SAVE_SUCCEEDED': {},
		/**
		 * Dispatched after a user has clicked save and the save operation has failed
		 *
		 * @type {MultiFormSaveFailedPayload}
		 */
		'MULTI_FORM#SAVE_FAILED': {},
		/**
		 * Dispatched when the dirty state of the form has changed, true for dirty, false for not dirty
		 *
		 * @type {boolean}
		 */
		'MULTI_FORM#DIRTY_CHANGED': {}
	},
	styles
});

export default WorkspaceMultiFormElement;
