import {
	Component,
	PropTypes,
	getInstanceId,
	React
} from '../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import _ from 'lodash';
import style from './Section.scss';
import FormSectionHeader from '../layout/FormSectionHeader';
import { controlElementFactory } from '@devsnc/sn-glide-form-controls';
import * as DependencyManager from '../../../config/DependencyManager';
import '@devsnc/sn-catalog-form';
import { GFORM_ACTIONS } from '@devsnc/sn-scripting-environment';
import classNames from 'classnames/dedupe';

class Section extends Component {
	constructor(props) {
		super(props);
		this.state = {
			instanceId: getInstanceId(props)
		};
	}

	shouldComponentUpdate(nextProps, nextState) {
		const compare = {
			fields: !_.isEqual(this.props.fields, nextProps.fields),
			section: this.props.section !== nextProps.section,
			sectionList: this.props.sectionList !== nextProps.sectionList,
			disableCollapse:
				this.props.disableCollapse !== nextProps.disableCollapse,
			headerBackground:
				this.props.headerBackground !== nextProps.headerBackground
		};
		return (
			compare.fields ||
			compare.section ||
			compare.sectionList ||
			compare.disableCollapse ||
			compare.headerBackground
		);
	}

	renderVariableEditor() {
		const VEditorElement = DependencyManager.getElementTag(
			'sn-catalog-form'
		);
		const {
			table,
			sysId,
			originalFields,
			serializedChanges,
			encodedRecord,
			isNewRecord,
			userSession
		} = this.props;
		const variablesLayout = _.get(
			originalFields,
			'variables.variablesLayout'
		);
		const variableFields = _.get(originalFields, 'variables.variables');
		// Retrieves the parsed catalog field variables
		const mappedVariablesList =
			variableFields &&
			variableFields.map(variable => {
				return originalFields[variable.name];
			});

		// Convert the catalog variables to an object and prepend IO to the key
		// This is a temporary solution till catalog form PRB is fixed
		const resultVariablesObj =
			mappedVariablesList &&
			mappedVariablesList.reduce(function(map, obj) {
				const key = obj.name;
				map[key] = obj;
				return map;
			}, {});

		const formData = {
			serializedChanges,
			encodedRecord,
			isNewRecord,
			userSession
		};
		return (
			<VEditorElement
				formData={formData}
				sourceTable={table}
				sourceId={sysId}
				fields={resultVariablesObj}
				variablesLayout={variablesLayout}
			/>
		);
	}

	render() {
		const {
			section,
			sectionList,
			dispatch,
			disableCollapse,
			inUiBuilder,
			headerBackground
		} = this.props;
		if (!section || section.visible === false) return null;
		return (
			<section
				className={classNames('sn-section', {
					'ws-form': !inUiBuilder
				})}
				key={'section-' + section.sysId}
			>
				<FormSectionHeader
					caption={section.caption}
					label={section.label}
					sectionList={sectionList}
					disableCollapse={disableCollapse}
					isVisible={section.expanded}
					headerBackground={headerBackground}
					dispatch={dispatch}
				>
					<div className="sn-section-body">
						{section.rows
							? this.renderRows(section.rows, dispatch)
							: this.renderVariableEditor()}
					</div>
				</FormSectionHeader>
			</section>
		);
	}

	renderRows(rows, dispatch) {
		if (!rows) {
			return null;
		}
		return rows.map((row, index) => {
			return (
				<div key={'row-' + index} className="sn-section-form-row">
					{this.renderColumns(row, dispatch)}
				</div>
			);
		});
	}

	renderColumns(columns, dispatch) {
		if (!columns) {
			return null;
		}
		const className = classNames({
			'sn-section-form-column' : columns.length !== 2,
			'sn-section-form-column equal-width' : columns.length === 2
		});
		return columns.map((columnFields, index) => {
			return (
				<div key={'col-' + index} className={className}>
					{this.renderFields(columnFields.fields, dispatch)}
				</div>
			);
		});
	}

	renderFields(columnFields, dispatch) {
		return columnFields.map(fieldName => {
			const {
				fields,
				serializedChanges,
				encodedRecord,
				isNewRecord,
				classicForm,
				userSession
			} = this.props;

			let field = null;
			if (fieldName) {
				field = fields[fieldName];
			}
			const type = field ? field.type : null;
			if (!DependencyManager.getFormControlState(type)) {
				return <div>{type} is not enabled!</div>;
			}
			if (type === 'variables') return;
			if (!field) return;
			return controlElementFactory.createFormControl(
				this.state.instanceId,
				type,
				field,
				(event, fieldName, value, displayValue, error) => {
					dispatch(GFORM_ACTIONS.GFORM_INTERNAL_BATCH_ACTIONS, {
						actions: [
							{
								name: GFORM_ACTIONS.GFORM_HIDE_FIELD_MESSAGE,
								payload: { fieldName, clearAll: true }
							},
							{
								name: GFORM_ACTIONS.GFORM_SET_VALUE,
								payload: { fieldName, value, displayValue }
							},
							...(error
								? [
										{
											name:
												GFORM_ACTIONS.GFORM_SHOW_FIELD_MESSAGE,
											payload: {
												fieldName,
												message: error.message,
												type: error.type
											}
										},
										...(error.type === 'error'
											? [
													{
														name:
															GFORM_ACTIONS.GFORM_INTERNAL_SET_FIELD_INVALID,
														payload: {
															fieldName,
															isInvalid: true
														}
													}
											  ]
											: [])
								  ]
								: [])
						]
					});
				},
				null,
				{
					serializedChanges,
					encodedRecord,
					isNewRecord,
					classicForm,
					userSession
				}
			);
		});
	}
}

Section.style = style;

Section.propTypes = {
	table: PropTypes.string,
	sysId: PropTypes.string,
	section: PropTypes.object,
	fields: PropTypes.object,
	serializedChanges: PropTypes.string,
	encodedRecord: PropTypes.string,
	isNewRecord: PropTypes.bool,
	disableCollapse: PropTypes.bool,
	sectionList: PropTypes.array,
	originalFields: PropTypes.object,
	dispatch: PropTypes.func,
	headerBackground: PropTypes.string,
	classicForm: PropTypes.bool,
	inUiBuilder: PropTypes.bool,
	userSession: PropTypes.object
};

Section.defaultProps = {
	disableCollapse: false
};

export default Section;
