import { createElement } from '../../utils/seismic';
import { actionTypes } from '@servicenow/ui-core';
import {
	getWrappedActionPayload,
	transformNotificationPayload
} from '../../utils/effect';
import {
	transformPreviewRecordPayload,
	dispatchPreviewRecord,
	createReDispatchActionHandler
} from '@devsnc/library-form';
import { default as ColumnLayout } from './ColumnLayout';
import { RESET_SELECTED_SECTION_INDEX } from './columnLayoutActions';
import styles from './ColumnLayout.scss';
const { COMPONENT_CONNECTED, COMPONENT_DOM_TREE_READY } = actionTypes;

const DA_WRAPPED_CLICKED = "DA_WRAPPED_CLICKED";

export const daWrapClickHandler = ({
	action: {
		payload
	},
	dispatch
}) => {
	const {
		wrapped_action_name,
		wrapped_payload
	} = payload;
	// set eventHandler here so that this component will catch this redispatch event before its parent.
	if (wrapped_action_name) {
		dispatch(wrapped_action_name, wrapped_payload, {eventHandler: true});
		// redispatch this for record page tabs to do event mapping
		dispatch('NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#WRAPPED_ACTION', payload);
	}
};

export default createElement(
	{
		name: 'FormSectionColumnLayout',
		component: ColumnLayout,

		properties: {
			/**
			 * Object containing the session user information
			 * @type {userSession}
			 */
			userSession: {},
			/**
			 * Map of fields {field_name: {}, ...}, each field object contains all the info to be passed to the field component which will be rendered
			 * @type {object}
			 */
			fields: {},
			/**
			 * array of section object, each object contains all the info about each section eg (id, order, rows, fields in each row)
			 * @type {Array.<object>}
			 */
			sections: {},
			/**
			 * Flag that indicates whether the component is rendered in a classic form
			 * @type {boolean}
			 */
			classicForm: {},
			/**
			 * whether or not the form is composed inside a ui builder page
			 * @type {boolean}
			 * @private
			 */
			inUiBuilder: {
				default: false,
				schema: {
					type: 'boolean'
				}
			},
			/**
			 * The serialized dirty fields string
			 * @type {string}
			 */
			serializedChanges: {},
			/**
			 * The encoded record from the server to be passed to the gliderecord when updating the record
			 * @type {string}
			 */
			encodedRecord: {},
			/**
			 * object containing properties for rendering the activity stream component
			 * @type {Object}
			 */
			activityStream: {},
			/**
			 * has config related values for the record
			 * @type {FormBlobConfig}
			 */
			config: { default: {} },
			/**
			 * Flag that indicates whether the record form is reloading
			 * @type {boolean}
			 */
			isReloading: {},
			/**
			 * Flag that indicates whether the record form is displaying any error messages
			 * @type {boolean}
			 */
			hasErrorMessage: {},
			/**
			 * Flag that indicates whether the record tab is active
			 * @type {boolean}
			 */
			active: {},
			/**
			 * specific view of the tables configured layout to render
			 * @type {string}
			 */
			view: {},
			/**
			 * The sys id of the record
			 * @type {string}
			 */
			sysId: {},
			/**
			 * The table that the record exists in
			 * @type {string}
			 */
			table: {},
			/**
			 * Object containing form layout configuration.
			 */
			formLayoutConfig: {},
			/**
			 * Flag that disables/enables section collapsing
			 */
			disableCollapse: {},
			/**
			 * Flag that indicates whether the section menu is shown
			 */
			isSectionMenuHidden: {},
			/**
			 * Host element which can be scrolled
			 * @type {Object}
			 */
			scrollableHost: {},
		},
		actionHandlers: {
			[DA_WRAPPED_CLICKED]: {
				effect: daWrapClickHandler,
				stopPropagation: false
			}, 
			[RESET_SELECTED_SECTION_INDEX]: {
				effect: ({ updateState }) => {
					updateState({
						selectedSectionIndex: -1,
					});
				},
				stopPropagation: true
			},
			'NOW_DROPDOWN#ITEM_CLICKED': {
				effect: ({ updateState, action: { payload } }) => {
					updateState({
						selectedSectionIndex: _.get(payload, 'item.id')
					});
				},
				stopPropagation: true
			},
			[COMPONENT_CONNECTED]: {
				effect: ({ properties: { inUiBuilder }, updateState, action: { payload: { host } } }) => {
					const hostBackgroundColor = window.getComputedStyle(host).backgroundColor;

					updateState({
						headerBackground: hostBackgroundColor,
						shouldRender: inUiBuilder
					});
				}
			},
			[COMPONENT_DOM_TREE_READY]: {
				effect({
					updateState,
					state: { properties },
					action: {
						payload: { host }
					}
				}) {
					
					updateState({
						host: host,
						shouldRender: false	
					});

					const { sections = [] } = properties;
					const index = sections.findIndex(
						section => section.defaultSection
					);
					if (index <= 0) return;

					const defaultSection = sections[index];

					if (defaultSection && defaultSection.id > -1) {
						const sectionList = host.shadowRoot.querySelectorAll(
							'.sn-section'
						);
						const desiredSection = sectionList[defaultSection.id];
						if (desiredSection) {
							desiredSection.scrollIntoView();
						}
					}
				}
			},
			PREVIEW_RECORD: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#PREVIEW_RECORD',
				transformPreviewRecordPayload
			),
			ITEM_SELECTED: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#ITEM_SELECTED'
			),
			ADD_NOTIFICATIONS: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#ADD_NOTIFICATIONS',
				transformNotificationPayload
			),
			REMOVE_NOTIFICATIONS: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#REMOVE_NOTIFICATIONS',
				transformNotificationPayload
			),
			CLEAR_NOTIFICATIONS: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#CLEAR_NOTIFICATIONS'
			),
			WRAPPED_ACTION: {
				effect: ({ dispatch, action }) => {
					dispatch(
						'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#WRAPPED_ACTION',
						getWrappedActionPayload(action)
					);
				},
				stopPropagation: true
			},
			SCREEN_STATUS_CHANGED: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#SCREEN_STATUS_CHANGED'
			),
			EXECUTE_UI_ACTION: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#EXECUTE_UI_ACTION'
			),
			REFRESH_REQUESTED: createReDispatchActionHandler(
				'NOW_RECORD_FORM_SECTION_COLUMN_LAYOUT#REFRESH_REQUESTED'
			)
		},
		styles
	},
	'public'
);
