import _ from 'lodash';
import {
	WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS,
	WORKSPACE_FORM_LAYOUT_ACTIONS,
	LIST_LISTEN_TYPES_INTERNAL_CAMMEL_CASE
} from './WorkspaceFormLayoutConst';
import {
	createActionToStateHandler,
	createActionPayloadToStateHandler
} from '@devsnc/library-form';

export default {
	actionHandlers: {
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_QUERY_UPDATED]: {
			effect: createActionToStateHandler(
				LIST_LISTEN_TYPES_INTERNAL_CAMMEL_CASE.LIST_QUERY_UPDATED
			),
			stopPropagation: true
		},
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_LIMIT_UPDATED]: {
			effect: createActionToStateHandler(
				LIST_LISTEN_TYPES_INTERNAL_CAMMEL_CASE.LIST_LIMIT_UPDATED
			),
			stopPropagation: true
		},
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_PAGE_UPDATED]: {
			effect: createActionToStateHandler(
				LIST_LISTEN_TYPES_INTERNAL_CAMMEL_CASE.LIST_PAGE_UPDATED
			),
			stopPropagation: true
		},
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_DATA_FETCH_COMPLETE]: {
			effect: createActionToStateHandler(
				LIST_LISTEN_TYPES_INTERNAL_CAMMEL_CASE.LIST_DATA_FETCH_COMPLETE
			),
			stopPropagation: true
		},
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.UPDATE_STATE]: {
			effect: ({ updateState, action }) => {
				updateState(action.payload);
			},
			stopPropagation: true
		},
		[WORKSPACE_FORM_LAYOUT_ACTIONS.DISABLE_FORM]: {
			effect: createActionPayloadToStateHandler(
				'disableForm',
				'disabled'
			),
			stopPropagation: true
		}
	}
};
