import {
	SIMPLE_FORM_HEADER_CLICK_ACTION,
	SIMPLE_FORM_FOOTER_CLICK_ACTION,
	QUICK_FORM_ACTIONS,
	FORM_SAVE_STATUS
} from '../../../constants/actions';
import {
	QUICK_FORM_INTERNAL_ACTIONS,
	TABS_ACTIONS
} from './WorkspaceQuickFormConstants';
import { GFORM_ACTIONS } from '@devsnc/sn-scripting-environment';
import { createEffectsInterceptors } from '@devsnc/library-form';

const SUBMIT_ACTION_NAME = 'sysverb_ws_save';

const handleFormClose = (updateState, dispatch, isDirty) => {
	!isDirty
		? dispatch(QUICK_FORM_ACTIONS.CLOSE)
		: updateState({
				displayModal: true
		  });
};

const enableForm = (isEnabled, dispatch) => {
	dispatch(QUICK_FORM_INTERNAL_ACTIONS.UPDATE_STATE_ACTION, {
		disableForm: !isEnabled
	});
};

const generateSaveDispatchPayload = function(
	isSuccess,
	fields,
	sysId,
	table,
	view,
	errorMessage
) {
	return {
		sysId,
		status: isSuccess ? FORM_SAVE_STATUS.SUCCESS : FORM_SAVE_STATUS.FAILURE,
		fields,
		table,
		view,
		error: !isSuccess ? { message: errorMessage } : null
	};
};

const handleSaveEvent = function({ fields, sysId, table, view }, dispatch) {
	enableForm(false, dispatch);

	dispatch(
		GFORM_ACTIONS.GFORM_SUBMIT,
		{
			submitActionName: 'sysverb_ws_save'
		},
		{
			callback: submitResult => {
				const dispatchError = () => {
					dispatch(
						QUICK_FORM_ACTIONS.SAVE_FAILED,
						generateSaveDispatchPayload(
							false,
							fields,
							sysId,
							table,
							view,
							`Error saving record: ${sysId}`
						)
					);
					enableForm(true, dispatch);
				};
				if (!submitResult) enableForm(true, dispatch);
				else
					submitResult
						.then(value => {
							dispatch(
								QUICK_FORM_ACTIONS.SAVE_SUCCEEDED,
								generateSaveDispatchPayload(
									true,
									fields,
									sysId,
									table,
									view
								)
							);
							enableForm(true, dispatch);
						})
						.catch(error => {
							dispatchError();
						});
			}
		}
	);
};

export default {
	actionHandlers: {
		[SIMPLE_FORM_HEADER_CLICK_ACTION]: {
			effect: ({
				updateState,
				state: {
					properties: { isDirty }
				},
				dispatch
			}) => {
				handleFormClose(updateState, dispatch, isDirty);
			},
			stopPropagation: true
		},
		[SIMPLE_FORM_FOOTER_CLICK_ACTION]: {
			effect: ({
				updateState,
				state: {
					properties,
					properties: { isDirty }
				},
				dispatch,
				action: { payload }
			}) => {
				if (payload.action === 'cancel')
					handleFormClose(updateState, dispatch, isDirty);
				else {
					handleSaveEvent(properties, dispatch);
				}
			},
			stopPropagation: true
		},
		[QUICK_FORM_INTERNAL_ACTIONS.UPDATE_STATE_ACTION]: {
			effect: ({ updateState, action: { payload } }) => {
				updateState({ payload });
			},
			stopPropagation: true
		},
		[TABS_ACTIONS.TAB_SELECTED]: {
			effect: ({ updateState, action: { payload } }) => {
				updateState({
					showFooter: payload.tabIndex === 0,
					selectedTabIndex: payload.tabIndex
				});
			},
			stopPropagation: true
		},
		[QUICK_FORM_INTERNAL_ACTIONS.DISCARD_MODAL]: {
			...createEffectsInterceptors([
				({ updateState }) => {
					updateState({ displayModal: false });
				},
				({ dispatch }) => {
					dispatch(QUICK_FORM_ACTIONS.CLOSE);
				}
			]),
			stopPropagation: true
		},
		'NOW_MODAL#OPENED_SET': {
			effect: ({ updateState }) => {
				updateState({ displayModal: false });
			},
			stopPropagation: true
		}
	}
};
