import { SIMPLE_FORM_HEADER_CLICK_ACTION } from '../../../constants/actions';

const NOW_BUTTON_CLICKED = 'NOW_BUTTON#CLICKED';

export default {
	[NOW_BUTTON_CLICKED]: {
		effect: ({ dispatch }) => {
			dispatch(SIMPLE_FORM_HEADER_CLICK_ACTION, {
				action: 'close'
			});
		},
		stopPropagation: true
	}
};
