import { createElement } from '../utils/seismic';
import { default as WorkspaceFormLayout } from './WorkspaceFormLayout';
import formLayoutActions from './WorkspaceFormLayoutAction';
import {
	userPreferencesBehavior,
	getUserPreference
} from 'sn-user-preferences';

import { SIDEBAR_ACTION } from '@servicenow/now-record-common';
import { SELECTED_LAYOUT_WILL_CHANGE } from '../../components/form/layout/columnLayoutActions';
import styles from './WorkspaceFormLayout.scss';
import { LAYOUT_TYPE } from '../form/layout/formLayoutHelper';
import {
	MIN_LEFT_COLUMN_WIDTH,
	MIN_RIGHT_COLUMN_WIDTH
} from '../form/layout/layoutConstants';
import { actionTypes } from '@servicenow/ui-core';
import { getProperty } from 'sn-uxpage-presource';
import { attachmentPreviewActions } from '@servicenow/now-record-common';
import { SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN } from "@devsnc/sn-scripted-modal";

const { COMPONENT_BOOTSTRAPPED } = actionTypes;
const SHOW_SIDEBAR = 'workspace.showAgentAssist';
const SHOW_RIBBON = 'workspace.showRibbon';
const FORM_LAYOUT_SYS_PROP = 'sn_workspace_form.default_form_layout';
const DEFAULT_LAYOUT_TYPE = getProperty(FORM_LAYOUT_SYS_PROP, LAYOUT_TYPE.HALF);
const DEFAULT_LEFT_MIN_WIDTH =
	DEFAULT_LAYOUT_TYPE === LAYOUT_TYPE.HALF
		? MIN_LEFT_COLUMN_WIDTH + MIN_RIGHT_COLUMN_WIDTH
		: MIN_LEFT_COLUMN_WIDTH;

export default createElement(
	{
		name: 'WorkspaceFormLayout',
		component: WorkspaceFormLayout,
		behaviors: [{ behavior: userPreferencesBehavior }],
		properties: {
			hideDetails: {},
			defaultTab: {},
			isLoadingRecord: {},
			isReloading: {},
			classicForm: {},
			table: {},
			sysId: {},
			view: {},
			config: {},
			isAttachmentUploadDisabled: {},
			serializedChanges: {},
			encodedRecord: {},
			handleUiActionClick: {},
			actionNodes: {},
			fixedButtonNumber: {},
			fields: {},
			sections: {},
			relatedLists: {},
			relatedItems: {},
			sidebarActions: {},
			recordTags: {},
			attachments: {},
			formModal: {},
			activityStream: {},
			currentUser: {},
			formHeader: {},
			ribbon: {},
			active: {},
			dehydrate: {},
			specialHandlingNote: {},
			workspaceConfigId: {},
			notifications: {},
			formTabs: {},
			selectedTabIndex: {},
			selectedLayoutType: {
				default: DEFAULT_LAYOUT_TYPE
			},
			formLayoutConfig: {},
			leftColumnMinWidth: {
				default: DEFAULT_LEFT_MIN_WIDTH
			},
			showSidebar: {
				computed: state =>
					getUserPreference(SHOW_SIDEBAR, state, 'false') === 'true'
			},
			showRibbon: {
				computed: state => {
					return (
						getUserPreference(SHOW_RIBBON, state, 'false') ===
						'true'
					);
				}
			},
			selectedTabName: {},
			templateRefresh: {}
		},
		initialState: {
			sidebarExpanded: true,
			isModalOpen: false
		},
		actionHandlers: {
			...formLayoutActions.actionHandlers,
			...attachmentPreviewActions.actionHandlers,
			[SN_SCRIPTED_MODAL_HOISTED_MODAL_IS_OPEN]: ({ updateState, action: { payload } }) => {
				updateState({
					isModalOpen: payload
				});
			},
			[SIDEBAR_ACTION.OPENED_SET]: ({ updateState, action }) => {
				const {
					payload: { value }
				} = action;
				updateState({
					sidebarExpanded: value
				});
			},
			[SELECTED_LAYOUT_WILL_CHANGE]: {
				effect: ({
					action: { payload },
					updateProperties,
					properties: { leftColumnMinWidth, selectedLayoutType }
				}) => {
					if (
						payload.minWidth !== leftColumnMinWidth ||
						payload.selectedLayoutType !== selectedLayoutType
					) {
						updateProperties({
							leftColumnMinWidth: payload.minWidth,
							selectedLayoutType: payload.selectedLayoutType
						});
					}
				},
				stopPropagation: true
			},
			[COMPONENT_BOOTSTRAPPED]: ({ state, properties, updateState }) => {
				if (
					properties.isLoadingRecord ||
					state.sidebarExpanded === properties.showSidebar
				)
					return;
				updateState({
					sidebarExpanded: properties.showSidebar
				});
			}
		},
		styles
	},
	'private'
);
