import { actionTypes } from '@servicenow/ui-core';
import _ from 'lodash';
import {
	WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS,
	TABS_ACTIONS
} from './WorkspaceFormLayoutConst';
import {
	getInitRelatedLists,
	getUpdatedRelatedList,
	getWorkspaceFormTabs
} from './formTabUtils';
const { COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

export const NOW_RECORD_FORM_BLOB_ACTIONS = {
	TAB_SELECTED: 'NOW_RECORD_FORM_BLOB#TAB_SELECTED',
	TAB_HEADER_COUNT_UPDATED: 'NOW_RECORD_FORM#ACTION_STATUS_UPDATED'
};

const resetStateProps = [
	'relatedLists',
	'table',
	'sysId',
	'view',
	'query',
	'workspaceConfigId',
	'fields',
	'sections',
	'relatedItems',
	'hideDetails',
	'formTabs',
	'isReloading',
	'hasErrorMessage',
	'defaultTab'
];

const getFormTabs = (componentId, properties, relatedLists) => {
	return getWorkspaceFormTabs(componentId, relatedLists, properties);
};

const updateRelatedList = ({
		state,
		properties,
		updateState,
		action,
		dispatch
	}) => {
	const { relatedLists: orgRelatedLists } = state;
	const {
		meta: { id = '' },
		type,
		payload
	} = action;

	const componentId =
		_.first(action.meta.path) || id.split('_form_related_list_')[0];

	const { relatedLists, updatedRelatedList } = getUpdatedRelatedList({
		relatedLists: orgRelatedLists,
		relatedListId: id,
		payload: payload,
		type
	});

	if (updatedRelatedList)
		dispatch(WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.UPDATE_RELATED_LIST_REQUEST, {
			name: updatedRelatedList.value,
			value: updatedRelatedList
		});

	action.stopPropagation();
	if (_.isEqual(relatedLists, orgRelatedLists)) return;
	const workspaceFormTabs = getFormTabs(
		componentId,
		properties,
		relatedLists
	);
	updateState({
		relatedLists,
		workspaceFormTabs
	});
};

const getSelectedTab = (selectedTabIndex, defaultTab, workspaceFormTabs) => {
	if (!_.isNil(selectedTabIndex) && selectedTabIndex > -1)
		return selectedTabIndex;

	if (defaultTab) {
		const defaultTabIndex = _.findIndex(
			workspaceFormTabs,
			tabOption => _.get(tabOption, 'tab.key') == defaultTab
		);
		return defaultTabIndex > -1 ? defaultTabIndex : 0;
	}
	return 0;
};

const initialRelatedList = (properties, state, updateState, componentId) => {
	const relatedLists = getInitRelatedLists(componentId, properties);
	const workspaceFormTabs = getFormTabs(
		componentId,
		properties,
		relatedLists
	);
	const selectedTab = getSelectedTab(
		properties.selectedTabIndex,
		properties.defaultTab,
		workspaceFormTabs
	);
	if (!state.selectedTabIndex || state.selectedTabIndex !== selectedTab)
		updateState({
			relatedLists,
			workspaceFormTabs,
			selectedTabIndex: selectedTab
		});
	else updateState({ relatedLists, workspaceFormTabs });
};

const selectTabByName = (selectedTabName, state, dispatch) => {
	const { workspaceFormTabs } = state;
	const selectedTabIndex = workspaceFormTabs.findIndex(
		tab =>
			tab.tab.key === selectedTabName || tab.tab.title === selectedTabName
	);

	if (selectedTabIndex === -1) return;

	dispatch('CONTENT_UPDATED', {
		params: { selectedTabIndex },
		redirectEmptyState: true
	});
};

export default {
	actionHandlers: {
		[COMPONENT_BOOTSTRAPPED]: ({
			properties,
			state,
			updateState,
			action
		}) => {
			const {
				meta: { id }
			} = action;
			initialRelatedList(properties, state, updateState, id);
		},
		[COMPONENT_PROPERTY_CHANGED]: context => {
			const {
				action: {
					payload: { name, value, previousValue },
					meta: { id }
				},
				updateState,
				state,
				properties,
				dispatch
			} = context;
			if (_.isEqual(value, previousValue)) return;

			if (resetStateProps.includes(name)) {
				initialRelatedList(properties, state, updateState, id);
			} else if (name === 'selectedTabName') {
				selectTabByName(value, state, dispatch);
			} else if (
				name === 'selectedTabIndex' &&
				(value !== previousValue && !isNaN(value))
			)
				updateState({ selectedTabIndex: value });
		},
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_QUERY_UPDATED]: updateRelatedList,
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_LIMIT_UPDATED]: updateRelatedList,
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_PAGE_UPDATED]: updateRelatedList,
		[WORKSPACE_FORM_LAYOUT_INTERNAL_ACTIONS.LIST_DATA_FETCH_COMPLETE]: updateRelatedList,
		[TABS_ACTIONS.TAB_SELECTED]: ({
			action,
			dispatch,
			updateState,
			state
		}) => {
			action.stopPropagation();
			const newIndex = _.get(action, 'payload.tabIndex');
			if (
				_.isUndefined(newIndex) ||
				newIndex <= -1 ||
				state.selectedTabIndex === newIndex
			)
				return;

			updateState({ selectedTabIndex: newIndex });

			const params = { selectedTabIndex: newIndex };

			dispatch('CONTENT_UPDATED', {
				params,
				redirectEmptyState: true
			});
		},
		[NOW_RECORD_FORM_BLOB_ACTIONS.TAB_SELECTED]: {
			effect: ({ action, dispatch, state }) => {
				const {
					payload: { selectedTabName }
				} = action;
				if (!selectedTabName) return;

				selectTabByName(selectedTabName, state, dispatch);
			},
			stopPropagation: true
		},
		[NOW_RECORD_FORM_BLOB_ACTIONS.TAB_HEADER_COUNT_UPDATED]: {
			effect: ({
				state,
				action: {
					payload: { component, type, value = null }
				},
				updateState
			}) => {
				const { workspaceFormTabs = {} } = state;
				if (type === 'badge') {
					const tabIndex = workspaceFormTabs.findIndex(
						formTab =>
							formTab.content.props.componentName &&
							formTab.content.props.componentName === component
					);
					if (tabIndex > -1) {
						updateState({
							path: `workspaceFormTabs[${tabIndex}].tab`,
							value: { count: value },
							operation: 'merge'
						});
					}
				}
			},
			stopPropagation: true
		}
	}
};
