import { createHttpEffect } from '@servicenow/ui-effect-http';
import { MULTI_UPDATE_INTERNAL_ACTIONS } from './updateActionsConst';

export const multiRecordUpdateHttpEffect = createHttpEffect(
	'/api/now/ui/glideRecord/:table',
	{
		method: 'PUT',
		pathParams: ['table'],
		successActionType: MULTI_UPDATE_INTERNAL_ACTIONS.SAVE_SUCCEEDED,
		errorActionType: MULTI_UPDATE_INTERNAL_ACTIONS.SAVE_FAILED
	}
);
