import { React, PureComponent, PropTypes } from '../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import _ from 'lodash';
import '@servicenow/now-legacy-icon';
import '@servicenow/now-tooltip';
import '@servicenow/now-tabs';
import { FORM_TAB_HEADER_EVENT_SOURCE } from '../../../constants/eventSources';

const initialState = tabs => {
	return {
		tabs: tabs.map((tab, index) => {
			return { label: tab.title, id: index, count: tab.count };
		}),
		moreTabs: [],
		moreLeftPosition: 0
	};
};

class TabHeaders extends PureComponent {
	constructor(props) {
		super(props);
		this.resize = this.resize.bind(this);
		this.tabRects = [];
		this.moreRect = {};
		this.state = initialState(props.properties.tabs);

		//when the tab is not active and it's called to rerender width, the container width will be 0, when the tab becomes active again from tab focus, it needs to rerender.
		this.needToReCalSize = false;
	}

	componentWillReceiveProps(nextProps) {
		const {
			properties: { tabs },
			behaviors: {
				resize: { size }
			}
		} = nextProps;
		const {
			properties: { moreOverflow },
			behaviors: {
				resize: { size: prevSize }
			}
		} = this.props;

		if (tabs && !_.isEqual(this.props.properties.tabs, tabs)) {
			this.setState(initialState(tabs));
		} else if (
			moreOverflow &&
			size.width &&
			size.width !== prevSize.width
		) {
			this.setState(initialState(this.props.properties.tabs));
		}
	}

	componentDidUpdate(prevProps) {
		const { moreOverflow } = this.props.properties;
		if (!moreOverflow) return;

		if (
			this.props.properties.selectedTabIndex !==
			prevProps.properties.selectedTabIndex
		)
			this.resize();
	}

	resize() {
		const { tabs: originalTabs } = this.state;
		const {
			behaviors: {
				resize: { size }
			}
		} = this.props;

		if (!size.width) return;

		const totalTabsWidth = this.tabRects.reduce((sum, { width }) => {
			return sum + width;
		}, 0);

		//Container rect can be changed, when resizing the browser or switching to the different tab.
		const maxWidth = size.width;
		//IE workaround
		const widthHasChanged = this.state.maxWidth !== maxWidth;

		//When tab is not active, width becomes 0
		if (maxWidth == 0) {
			this.needToReCalSize = true;
			return false;
		}
		this.needToReCalSize = false;

		let moreTabs = [];
		let sumWidth = 0;
		let moreLeftPosition = 0;
		let changed = false;
		const showMore = maxWidth < totalTabsWidth;
		const moreWidth = showMore ? this.moreRect.width : 0;
		if (this.tabRects.length !== originalTabs.length) return false;

		const tabs = originalTabs.map((tab, index) => {
			sumWidth += this.tabRects[index].width;
			let isHidden = false;
			if (sumWidth + moreWidth > maxWidth) {
				isHidden = true;
				moreTabs.push(tab);
				if (!moreLeftPosition)
					moreLeftPosition = sumWidth - this.tabRects[index].width;
			}

			if (originalTabs[index].isHidden !== isHidden) changed = true;

			return {
				...tab,
				isHidden
			};
		});

		//only set state if changed.
		if (changed || widthHasChanged) {
			this.setState({
				tabs,
				moreTabs,
				moreLeftPosition,
				maxWidth
			});
		}
		return true;
	}

	render() {
		const { tabs } = this.state;
		const { componentId } = this.props;
		if (!tabs) return null;
		return (
			<now-tabs
				items={tabs}
				component-id={`${componentId}_nowtab`}
				selected-item={this.props.properties.selectedTabIndex}
				append-to-payload={{
					eventSource: FORM_TAB_HEADER_EVENT_SOURCE
				}}
			/>
		);
	}
}

TabHeaders.propTypes = {
	tabs: PropTypes.array,
	moreOverflow: PropTypes.bool,
	selectedTabIndex: PropTypes.number,
	showTooltips: PropTypes.bool,
	tabPanelIds: PropTypes.array
};

export default TabHeaders;
