import { t } from 'sn-translate';
import react from '@servicenow/ui-renderer-react';
import '@servicenow/now-alert';

export default function renderReadonlyNotification() {
	const action = {
		type: ''
	};
	const content = t(
		'This form has not been configured for Workspace. To edit the form, select a different type of record, or contact your administrator.'
	);
	const icon = 'info-circle-outline';
	const status = 'info';
	return (
		<div className="readonly-notification-container">
			<now-alert
				status={status}
				icon={icon}
				content={content}
				action={action}
				expanded={true}
			/>
		</div>
	);
}
