import snabbdom from '@servicenow/ui-renderer-snabbdom';
import { createCustomElement } from '@servicenow/ui-core';
// eslint-disable-next-line no-unused-vars
import { nowRecordListConnectedRelated } from '@servicenow/now-record-list-connected';

const RELATED_LIST_TAB_CONTENT = 'now-record-related-list-tab-content';
const getRelatedListComponentId = (componentIdPrefix, index) => {
	return `${componentIdPrefix}_related_list_${index}`;
};
const RELATED_LIST = 'now-record-list-connected-related';

const getRelatedListProps = (relatedList, properties) => {
	const {
		table,
		sysId,
		view,
		config = {},
		workspaceConfigId,
		relatedListLimit,
		wrapListText,
		isNewRecord,
		readonly
	} = properties;

	const props = {
		limit: relatedListLimit,
		wordWrap: wrapListText,
		hideNew: readonly,
		workspaceConfigId,
		view,
		table,
		sysId
	};

	const relatedListProps = {
		table: relatedList.table,
		listTitle: relatedList.displayName,
		parentTable: relatedList.parentTable,
		parentRecordSysId: sysId,
		isParentReadOnly: readonly,
		isParentNewRecord: isNewRecord,
		relatedListName: relatedList.value,
		query: _.get(relatedList, 'query', relatedList.filter),
		page: _.get(relatedList, 'page', 1),
		listType: 'RELATED',
		workspaceConfigId
	};
	// add related lists
	return _.merge({}, props, relatedListProps);
};

const getRelatedListPropsAtIndex = (index, properties) => {
	const { relatedLists = [] } = properties;
	if (index < 0 || index >= relatedLists.length) return {};
	return getRelatedListProps(relatedLists[index], properties);
};

const view = (state, { dispatch, updateState }) => {
	const {
		properties,
		properties: { index },
		componentId
	} = state;

	const props = getRelatedListPropsAtIndex(index, properties);
	const relatedListComponentId = getRelatedListComponentId(
		componentId,
		index
	);
	return <RELATED_LIST component-id={relatedListComponentId} {...props} />;
};

createCustomElement(RELATED_LIST_TAB_CONTENT, {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/*
		 * array of related list data
		 */
		relatedLists: {
			schema: {
				type: 'array',
				items: [
					{
						type: 'object',
						properties: {
							table: { type: 'string' },
							displayName: { type: 'string' },
							parentTable: { type: 'string' },
							value: { type: 'string' },
							filter: { type: 'string' }
						},
						additionalProperties: true
					}
				]
			}
		},
		/*
		 * index of rendering related list
		 */
		index: { default: 0, schema: { type: 'number' } },
		/*
		 * whether a record is read only or not
		 */
		readonly: { default: false, schema: { type: 'boolean' } },
		/*
		 * whether the record is a new record
		 */
		isNewRecord: { default: false, schema: { type: 'boolean' } },
		/*
		 * whether to allow to word wrap
		 */
		wrapListText: { schema: { type: 'boolean' } },
		/*
		 * number of items in the list per page
		 */
		relatedListLimit: { default: 20, schema: { type: 'number' } },
		/*
		 * workspace id
		 */
		workspaceConfigId: { schema: { type: 'string' } },
		/*
		 * view
		 */
		view: { schema: { type: 'string' } },
		/*
		 * table of the record
		 */
		table: { schema: { type: 'string' } },
		/*
		 * sysId of the record
		 */
		sysId: { schema: { type: 'string' } }
	}
});

export default RELATED_LIST_TAB_CONTENT;
