import { addClassName, removeClassName } from './dom';
import { keyUtils } from '@devsnc/library-form';
import { resizingFlex } from './resizingFlex';
import { resizingDimension } from './resizingDimension';

const RESIZABLE_BASE_CLASS = 'resizable-controller';
const RESIZING_STATE_CLASS = 'state-resizing';
const RESIZABLE_CONTAINER = 'resizable-container';
const RESIZABLE_LEFT_COLUMN = 'resizable-left-column';
const KEY_SIZE = 4;

export const RESIZE_TYPE = {
	FLEX: 1,
	DIMENSION: 2
};

export const createResizableController = (
	containerEl,
	leftColumnEl,
	resizeBarEl,
	resizeKey,
	options = {}
) => {
	let isResizing = false;
	let leftColumnWidth = 0;
	let maxResizableWidth = 0;
	let resizingAlg;
	let prevX = 0;
	let currentWidth;

	const {
		minLeftColumnWidth = 0,
		minRightColumnWidth = 0,
		resizeType = RESIZE_TYPE.FLEX,
		allowKeyAccess = false,
		beforeStartResizing = () => {},
		afterStopResizing = () => {}
	} = options;

	const isInResizeBounds = width => {
		return (
			width <= maxResizableWidth - minRightColumnWidth &&
			width >= minLeftColumnWidth
		);
	};

	const resizing = (movementX = 0) => {
		if (!isResizing) return;

		const newWidth = leftColumnWidth + movementX;
		if (isInResizeBounds(newWidth)) {
			leftColumnWidth = newWidth;
			resizingAlg.resizing(leftColumnWidth, maxResizableWidth);
		}
	};

	const startResizing = () => {
		isResizing = true;
		beforeStartResizing();
		leftColumnWidth = leftColumnEl.getBoundingClientRect().width;
		maxResizableWidth = containerEl.getBoundingClientRect().width;
		if (!maxResizableWidth) return;
		addClassName(leftColumnEl, RESIZING_STATE_CLASS);
		addClassName(containerEl, RESIZING_STATE_CLASS);
		requestAnimationFrame(() => {
			resizing(0);
		});
	};

	const mouseMove = event => {
		if (!isResizing) return;

		let { movementX } = event;

		if (movementX === undefined)
			movementX = prevX ? event.screenX - prevX : 0;
		prevX = event.screenX;

		if (!movementX) return;

		requestAnimationFrame(() => {
			resizing(movementX);
		});
		event.preventDefault();
	};

	const stopResizing = () => {
		if (!isResizing) return;

		currentWidth = resizingAlg.getLastSize();
		isResizing = false;
		removeClassName(containerEl, RESIZING_STATE_CLASS);
		afterStopResizing(currentWidth);
	};

	const keyPress = event => {
		keyUtils.onEnter(event, () => {
			startResizing();
			resizeBarEl.addEventListener('keydown', resizingKeyDown);
		});
	};

	const focusOnBar = () => {
		resizeBarEl.addEventListener('keypress', keyPress);
	};

	const blurOnBar = () => {
		resizeBarEl.removeEventListener('keypress', keyPress);
	};

	const hasValidElements = () => {
		return containerEl && leftColumnEl && resizeBarEl;
	};

	const disableResizing = () => {
		removeClassName(leftColumnEl, RESIZING_STATE_CLASS);
	};

	const enableResizing = () => {
		addClassName(leftColumnEl, RESIZING_STATE_CLASS);
	};

	const setWidth = width => {
		if (width !== currentWidth) {
			currentWidth = width;
			resizingAlg.resizingWithRawValue(width);
		}
	};

	function resizingKeyDown(event) {
		keyUtils.onLeftArrow(event, () => {
			event.preventDefault();
			resizing(-KEY_SIZE);
		});

		keyUtils.onRightArrow(event, () => {
			event.preventDefault();
			resizing(KEY_SIZE);
		});

		if (keyUtils.onEscape(event) || keyUtils.onTab(event)) {
			resizeBarEl.removeEventListener('keydown', resizingKeyDown);
			stopResizing();
		}
	}

	const mouseUp = () => {
		if (!isResizing) return;

		stopResizing();
		removeMouseResizingEvents();
	};

	const addMouseResizingEvents = () => {
		document.addEventListener('mousemove', mouseMove);
		document.addEventListener('mouseup', mouseUp);
	};

	function removeMouseResizingEvents() {
		document.removeEventListener('mousemove', mouseMove);
		document.removeEventListener('mouseup', mouseUp);
	}

	const mouseDown = event => {
		event.preventDefault();
		event.stopPropagation();
		prevX = event.screenX;
		startResizing();
		addMouseResizingEvents();
	};

	const addListeners = () => {
		if (!hasValidElements()) {
			console.warn(
				'Invalid elements:',
				containerEl,
				leftColumnEl,
				resizeBarEl
			);
			return;
		}
		resizeBarEl.addEventListener('mousedown', mouseDown);
		if (allowKeyAccess) {
			resizeBarEl.addEventListener('focus', focusOnBar);
			resizeBarEl.addEventListener('blur', blurOnBar);
		}
	};

	const removeListeners = () => {
		resizeBarEl.removeEventListener('mousedown', mouseDown);
		resizeBarEl.removeEventListener('focus', focusOnBar);
		if (allowKeyAccess) {
			resizeBarEl.removeEventListener('blur', blurOnBar);
			resizeBarEl.removeEventListener('keypress', keyPress);
		}
	};

	const initialize = () => {
		if (!hasValidElements()) return;

		resizingAlg =
			resizeType === RESIZE_TYPE.DIMENSION
				? resizingDimension(leftColumnEl)
				: resizingFlex(leftColumnEl);
		addClassName(containerEl, RESIZABLE_BASE_CLASS);
		addClassName(leftColumnEl, RESIZABLE_BASE_CLASS);
		addClassName(containerEl, RESIZABLE_CONTAINER);
		addClassName(leftColumnEl, RESIZABLE_LEFT_COLUMN);
		addClassName(containerEl, resizingAlg.resizeClassName);
		addClassName(leftColumnEl, resizingAlg.resizeClassName);
		addListeners();
	};

	const destroy = () => {
		removeListeners();
		leftColumnEl.removeAttribute('style');
		removeClassName(containerEl, RESIZING_STATE_CLASS);
		removeClassName(containerEl, RESIZABLE_BASE_CLASS);
		removeClassName(leftColumnEl, RESIZING_STATE_CLASS);
		removeClassName(leftColumnEl, RESIZABLE_BASE_CLASS);
		removeClassName(containerEl, RESIZABLE_CONTAINER);
		removeClassName(leftColumnEl, RESIZABLE_LEFT_COLUMN);
		removeClassName(containerEl, resizingAlg.resizeClassName);
		removeClassName(leftColumnEl, resizingAlg.resizeClassName);
	};

	return {
		initialize,
		destroy,
		setWidth,
		disableResizing,
		enableResizing
	};
};
