import { PureComponent, PropTypes, propsToAttributes } from '../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import _ from 'lodash';
import { default as TabHeadersElement } from './header/TabHeadersElement';
import { default as TabContentsElement } from './content/TabContentsElement';

