export const TabsBasePropTypes = {
	style: PropTypes.any,
	context: PropTypes.string,
	contextModifier: PropTypes.string,
	tabOptions: PropTypes.array,
	moreOverflow: PropTypes.bool,
	tabSelected: PropTypes.func,
	showTooltips: PropTypes.bool,
	selectedTab: PropTypes.number,
	tabContentExtras: PropTypes.object,
	formLayoutConfig: PropTypes.object
};

class Tabs extends PureComponent {
	constructor(props) {
		super(props);
		this.state = this.parseOptions(props.properties);
	}

	componentDidUpdate(prevProps) {
		if(_.get(prevProps, 'properties.formLayoutConfig.formSizeRatio') !== _.get(this.props, 'properties.formLayoutConfig.formSizeRatio')){
			const newContents = this.state.contents.map((item, index) => {
				const {props} = item;
				if(item.component === "now-record-form-section-column-layout"){
					return {
						...item,
                        props: {
							...props,
							formLayoutConfig: this.props.properties.formLayoutConfig
						}
					}	
				}
				return item;
			})
			this.setState({contents: newContents});
		 }
	}

	componentWillReceiveProps(changedProps) {
		const {
			properties: { tabOptions, selectedTab: propSelectedTab },
			selectedTab
		} = changedProps;
		if (tabOptions !== this.props.properties.tabOptions) {
			this.setState(this.parseOptions(changedProps.properties));
		}
		if (
			selectedTab !== undefined &&
			selectedTab != this.state.selectedTab
		) {
			// for snabdomm component
			this.setState({ selectedTab: selectedTab || 0 });
		} else if (propSelectedTab !== undefined) {
			// for react component
			this.setState({ selectedTab: propSelectedTab || 0 });
		}
	}

	parseOptions(props) {
		const { tabs: prevTabs = [], contents: prevContents = [] } =
			this.state || {};
		const { tabOptions = [], moreOverflow, selectedTab = 0 } = props;
		const contents = [];
		let tabChanged = prevTabs.length !== tabOptions.length;
		let contentChanged = prevContents.length !== tabOptions.length;
		const keyToIndexMap = {};
		const tabs = tabOptions.map((tabOption, index) => {
			const { tab, content } = tabOption;
			contents.push(content);
			contentChanged = content !== prevContents[index] || contentChanged;
			tabChanged = tab !== prevTabs[index] || tabChanged;
			keyToIndexMap[tab.key] = index;
			return tab;
		});
		return {
			tabs: tabChanged ? tabs : prevTabs,
			contents: contentChanged ? contents : prevContents,
			selectedTab: selectedTab >= tabs.length ? 0 : selectedTab,
			moreOverflow
		};
	}

	get baseInstanceId() {
		return this.props.componentId.split('_form')[0];
	}

	render() {
		const { tabs, contents, selectedTab, moreOverflow } = this.state;
		if (!tabs) {
			return null;
		}
		const componentId = this.props.componentId;
		const {
			context,
			contextModifier,
			style,
			showTooltips,
			tabContentExtras,
			formLayoutConfig
		} = this.props.properties;
		const className = `context-${context} context-modifier-${contextModifier}`;
		const tabPanelIds = contents.map(
			(content, index) =>
				`tab_panel_${index}_${_.get(content, 'props.componentId')}`
		);

		const headerAttrs = propsToAttributes({
			componentId: `${componentId}_header`,
			context,
			contextModifier,
			className,
			tabs,
			selectedTabIndex: selectedTab,
			moreOverflow,
			showTooltips,
			tabPanelIds
		});
		const contentAttrs = propsToAttributes({
			componentId: `${componentId}_content`,
			context,
			contextModifier,
			className,
			contents,
			tabPanelIds,
			selectedTabIndex: selectedTab,
			tabContentExtras,
			formLayoutConfig
		});
		return (
			<section style={style} className="sn-tab-container">
				<TabHeadersElement {...headerAttrs} />
				<TabContentsElement {...contentAttrs} />
			</section>
		);
	}
}

Tabs.propTypes = TabsBasePropTypes;

export default Tabs;
