import { PureComponent, PropTypes } from '../../utils/seismic';
import react from '@servicenow/ui-renderer-react';
import '@servicenow/now-button';
import '@servicenow/now-tooltip';
import _ from 'lodash';

const SELECT_CLASS_NAME = 'is-active';
const BUTTON_ALIGNMENT = {
	LEFT: 1,
	RIGHT: 2,
	CENTER: 3
};

class Toolbar extends PureComponent {
	constructor(props) {
		super(props);
		this.buttonRefs = _.fill(
			Array(props.properties.buttons.length || 0),
			null
		);
	}

	clickButton = e => {
		const { onButtonClick, buttons } = this.props.properties;
		if (!onButtonClick) return;

		const index = parseInt(e.currentTarget.dataset.index, 10);
		onButtonClick(buttons[index]);
	};

	setButtonRef = node => {
		if (!node) return;
		const index = node.getAttribute('data-index') * 1;
		this.buttonRefs[index] = node;
		if (this.buttonRefs.findIndex(ref => ref === null) === -1) {
			this.setState({ buttonRefs: this.buttonRefs });
		}
	};

	get alignClassName() {
		const { buttonAlignment } = this.props.properties;
		switch (buttonAlignment) {
			case BUTTON_ALIGNMENT.LEFT:
				return 'toolbar-align-left';
			case BUTTON_ALIGNMENT.CENTER:
				return 'toolbar-align-center';
			case BUTTON_ALIGNMENT.RIGHT:
			default:
				return 'toolbar-align-right';
		}
	}

	render = () => {
		const className = `sn-form-toolbar ${this.alignClassName}`;
		return <div className={className}>{this.renderButtons()}</div>;
	};

	getSelectedClassName(index) {
		const { selectedIndex } = this.props;
		return index === selectedIndex ? SELECT_CLASS_NAME : '';
	}

	renderButtons = () => {
		const { buttons, selectedIndex } = this.props.properties;
		if (buttons && buttons.length > 0) {
			return buttons.map((button, index) => (
				<button
					type="button"
					id={button.id}
					className={`sn-form-toolbar-btn ${this.getSelectedClassName(
						index
					)}`}
					aria-label={button.label}
					aria-selected={index === selectedIndex}
					data-index={index}
					key={button.id}
					component-name={button.id}
					tooltip-content={button.label}
					onClick={this.clickButton}
					data-index={index}
					ref={this.setButtonRef}
				>
					<now-icon icon={button.type} size="md" />
					<now-tooltip
						target-ref={this.buttonRefs[index]}
						content={button.label}
					/>
				</button>
			));
		}
	};
}

Toolbar.propTypes = {
	buttons: PropTypes.arrayOf(
		PropTypes.shape({
			label: PropTypes.string,
			id: PropTypes.string.isRequired,
			type: PropTypes.string.isRequired,
			disabled: PropTypes.bool
		})
	),
	selectedIndex: PropTypes.number,
	buttonAlignment: PropTypes.oneOf(Object.values(BUTTON_ALIGNMENT)),
	onButtonClick: PropTypes.func
};

Toolbar.defaultProps = {
	buttonAlignment: BUTTON_ALIGNMENT.RIGHT
};

export default Toolbar;
