import React from 'react';
import react from '@servicenow/ui-renderer-react';
import PropTypes from 'prop-types';
import isFunction from 'lodash/isFunction';
import isEmpty from 'lodash/isEmpty';
import style from './AlertList.scss';
import { style as iconStyle } from '@servicenow/now-legacy-icon';
import { t } from 'sn-translate';

class AlertList extends React.PureComponent {
	constructor(props) {
		super(props);
	}

	referenceDismissAll = node => {
		this.dismissAll = node;
		if (this.dismissAll) {
			this.dismissAll.focus();
		}
	};

	onPressDismissAll = e => {
		const charCode = e.keyCode || e.which;
		if (charCode === 13) {
			const { onDismissAll } = this.props.properties;
			if (isFunction(onDismissAll)) {
				onDismissAll();
			}
		}
	};

	componentWillReceiveProps(nextProps) {
		if (
			nextProps.dismissClicked &&
			nextProps.dismissClicked != this.props.dismissClicked
		) {
			const {
				meta: { componentName }
			} = nextProps.dismissClicked;
			if (componentName)
				this.props.properties.onDismiss(componentName * 1);
		}
	}

	render() {
		const { notifications, onClickHandler } = this.props.properties;
		if (isEmpty(notifications)) return null;
		return (
			<div className="sn-alert-container" onClick={onClickHandler} role="alertDialog" aria-live="polite">
				{notifications.length > 1 && this.renderDismiss()}
				{notifications.map((notification, index) => {
					return (
						<now-alert
							content={notification.content}
							action={{ type: 'dismiss' }}
							icon={notification.icon}
							status={notification.status}
							component-name={notification.originalIndex.toString()}
							key={index}
							expanded
						/>
					);
				})}
			</div>
		);
	}

	renderDismiss() {
		return (
			<div
				className="sn-alert sn-alert__dismissal"
				role="button"
				tabIndex="0"
				onClick={this.props.properties.onDismissAll}
				ref={this.referenceDismissAll}
				onKeyPress={this.onPressDismissAll}
			>
				<div className="sn-alert--content">
					<span>{t('Dismiss all notifications')}</span>
				</div>
			</div>
		);
	}
}

AlertList.propTypes = {
	notifications: PropTypes.array,
	onDismiss: PropTypes.func,
	onDismissAll: PropTypes.func,
	onClickHandler: PropTypes.func
};

AlertList.style = `${style} ${iconStyle}`;

export default AlertList;
