import { default as TabsElement } from '../tabs/TabsElement';
import { t } from 'sn-translate';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import WorkspaceForm from './WorkspaceForm';
import { EmptyStateElement } from '@servicenow/now-record-common';

const renderFormEmptyMessage = () => {
	return (
		<div className="sn-workspace-form-layout-empty-message">
			<span>
				{t(
					"If information doesn't appear on this tab, it may be hidden on purpose. Please contact your system administrator for more information."
				)}
			</span>
		</div>
	);
};

export const view = state => {
	const {
		componentId,
		workspaceFormTabs,
		properties: { defaultTab, fields, classicForm, tabContentExtras, formLayoutConfig },
		selectedTabIndex
	} = state;

	if (!classicForm && !fields)
		return <EmptyStateElement message={t('No data to display')} />;

	if (!workspaceFormTabs) return null;

	if (workspaceFormTabs.length == 0) return renderFormEmptyMessage();

	return (
		<sn-form-internal-tabs
			context="tab"
			context-modifier="full-container"
			className="context-tab context-modifier-full-container"
			seismic-style="related"
			tab-options={workspaceFormTabs}
			default-tab={defaultTab}
			more-overflow
			show-tooltips
			component-id={`${componentId}_formtabs`}
			selected-tab={selectedTabIndex}
			tab-content-extras={tabContentExtras}
			form-layout-config={formLayoutConfig}
		/>
	);
};
